<template>
    <div class="registration page-wrapper">
        <!-- Using button-content slot -->
        <template slot="button-content">
        <b>Entrar ou cadastrar</b>
        </template>
        <!-- <b-dropdown-item> -->
        <div class="login-dropdown-content">
            <b-row>
                <div class="col-md-2">
                  
                </div>
                <div class="col-md-7">
                    <h4 class="title"><i class="fa fa-comments pr-2"></i> Fale Conosco</h4><br/>
                    <h4 class="sub-title">Em caso de dúvida ou sugestão, por favor entre em contato conosco com o formulário abaixo.</h4>
                    <br/>
                    <b-form>
                        <div class="row">
                          <div class="form-group col-md-12">
                              <label for="name" class="label-input">Nome</label>
                              <b-form-input class="form-control input-form mr-sm-2" type="text" name="name" required v-model="user.name"></b-form-input>                   
                          </div>
                          <div class="form-group col-md-6">
                              <label for="email" class="label-input">Email</label>
                              <b-form-input class="form-control input-form mr-sm-2" type="text" required v-model="user.email"></b-form-input>
                          </div>
                          <div class="form-group col-md-6">
                              <label for="email" class="label-input">Telefone</label>
                              <the-mask class="form-control input-form" v-model="user.phone" masked="true" :mask="['(##) ####-####', '(##) #####-####']" />
                          </div>
                          <div class="form-group col-md-12">
                              <label for="email" class="label-input">Motivo</label>
                              <b-form-input class="form-control input-form mr-sm-2" type="text" required v-model="user.reason"></b-form-input>
                          </div>
                          <div class="form-group col-md-12">
                              <label for="email" class="label-input">Descrição</label>
                              <textarea class="form-control input-form mr-sm-2" v-model="user.message" rows="6" cols="50"></textarea>
                          </div>
                        </div>

                        <div class="form-group">
                            <b-button class="btn btn-theme button-send" @click="send()">ENVIAR</b-button>
                        </div>    
                    </b-form>
                </div>
            </b-row>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      USER: 'user'
    })
  },
  data () {
    return {
      user: {
        name: '',
        email: '',
        phone: '',
        reason: '',
        message: ''
      }
    }
  },
  methods: {
    send () {
      let json = this.user
      json.message = 'Motivo: ' + json.reason + ' Mensagem: ' + json.message
      this.$http.post('/help', json).then((response) => {
        response = response.data
        if (response.status) {
          this.user = {}
          this.$toasted.success(response.message).goAway(3000)
        } else {
          this.$toasted.error(response.message).goAway(3000)
        }
      })
    }
  },
  created () {
    if (this.USER && this.USER.email) {
      this.user = {
        name: this.USER.first_name + ' ' + this.USER.last_name,
        email: this.USER.email,
        phone: this.USER.phone && this.USER.phone.length > 0 ? this.USER.phone : ''
      }
    }
  }
}
</script>

<style scoped>
.navbar-light .navbar-nav .nav-link {
    color: black;
}

.login-dropdown-content {
    min-width: 250px;
    padding: 14px 14px 0;
    overflow: hidden;
}

.login-dropdown-content .bottom {
    border-top: 1px solid #ddd;
    clear: both;
    padding: 14px;
    width: 100%;
}

.login-dropdown-content .social-buttons {
    margin: 12px 0;
}

.login-dropdown-content .social-buttons button {
    width: 100%;
}

.login-dropdown-content .form-group {
    margin-bottom: 10px;
}

.title {
  color: #4f4f4f;
  font-weight: 600;
  font-size: 25px;
  padding-top: 20px;
}

.sub-title {
  font-size: 24px;
  line-height: 30px;
  color: #969696;
  margin: 0 0 25px 0;
  text-align: center;
}

.label-input {
  font-size: 22px;
}

.btn-fb:hover {
    color: #fff;    
    background-image: linear-gradient(#4C69BA, #3B55A0);
}

@media(max-width:768px) {
    .login-dropdown-content {
        background-color: inherit;
        color: black;
    }
    .login-dropdown-content .bottom {
        background-color: inherit;
        border-top: 0 none;
    }
}

.input-form {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-send {
  border: 0 !important;
  color: white;
  height: 50px;
  line-height: 20px;
  border-radius: 8px;
  box-sizing: border-box;
  font-size: 1.6em;
  font-weight: normal;
  padding-bottom: 12px;
  padding-top: 12px;
  width: 15%;
  background-color: #ffb42d;
  float: right;
  margin-bottom: 10%;
}
</style>



