<template>
<!-- Modal -->
<b-modal id="order-modal" hide-header size="lg">
    <b-btn class="close" @click="hideModal">
        <span aria-hidden="true"><i class="fa fa-times"></i></span>
    </b-btn>
    <b-container fluid class="cart-addon">
        <b-row>
            <div class="food-item white">
                <b-row>
                    <div class="col-xs-12 col-sm-6 col-lg-6">
                        <div class="item-img pull-left">
                            <a class="restaurant-logo pull-left">
                              <!-- food logo 70x70 -->
                                <img src="http://placehold.it/70x70" alt="Food logo">
                            </a>
                        </div>
                        <!-- end:Logo -->
                        <div class="rest-descr">
                            <h6>
                                <a>Sandwich de Alegranza Grande Menü (28 - 30 cm.)</a>
                            </h6>
                        </div>
                        <!-- end:Description -->
                    </div>
                    <!-- end:col -->
                    <div class="col-xs-6 col-sm-2 col-lg-2 text-sm-center">
                        <span class="price pull-left">$ 2.99</span>
                    </div>
                    <div class="col-xs-6 col-sm-4 col-lg-4">
                        <div class="row no-gutter">
                            <!-- <div class="col-xs-12"> -->
                                <div class="col-7">
                                    <select class="form-control b-r-0" id="exampleSelect2">
                                        <option>Size SM</option>
                                        <option>Size LG</option>
                                        <option>Size XL</option>
                                    </select>
                                </div>
                                <div class="col-5">
                                    <input class="form-control" type="number" value="0" id="quant-input-2">
                                </div>
                                <!-- <b-input-group>
                                    <b-input-group-button slot="right">
                                        <select class="form-control b-r-0" id="exampleSelect2">
                                            <option>Size SM</option>
                                            <option>Size LG</option>
                                            <option>Size XL</option>
                                        </select>
                                        <input class="form-control" type="number" value="0" id="quant-input-2">
                                    </b-input-group-button>

                                </b-input-group> -->
                            <!-- </div> -->
                        </div>
                    </div>
                </b-row>
                <!-- end:row -->
            </div>
            <!-- end:Food item -->
        </b-row>
    </b-container>
    <div slot="modal-footer">
        <b-btn variant="outline-primary" @click="hideModal">Fechar</b-btn>
        <b-btn variant="theme"><i class="fa fa-shopping-cart mr-2"></i>Adicionar ao carrinho</b-btn>
    </div>
</b-modal>
</template>

<script>
export default {
  data () {
    return {}
  },
  methods: {
    hideModal () {
      this.$root.$emit('bv::hide::modal', 'order-modal')
    }
  }
}
</script>

<style scoped>
/*============================================
* Modal
=============================================*/

.btn {
    cursor: pointer;
}

.cart-addon .food-item {
    margin-bottom: 20px;
    border-bottom: 1px solid #ededed;
    padding: 2px 0 15px;
}

.cart-addon .food-item:last-of-type {
    border-bottom: none;
}

.cart-addon .food-item .item-img img {
    max-width: 70px;
}

.cart-addon .food-item .rest-descr {
    margin-left: 80px;
}

.cart-addon .food-item h6 a {
    font-size: 16px;
    color: #4d4f56;
    display: block;
    margin: 4px 0 0 0;
    line-height: 20px;
}

.cart-addon .food-item .price {
    font-weight: 600;
    margin-top: 15px;
    display: block;
    color: #25282b;
}

.cart-addon .food-item .form-control {
    margin-top: 8px;
}

button.close {
    padding: 0;
    cursor: pointer;
    background: 0 0;
    border: 0;
    -webkit-appearance: none;
    right: -15px;
    position: absolute;
    top: -15px;
    background-color: #ffb42d;
    opacity: 1;
    width: 30px;
    height: 30px;
    color: #fff;
    text-shadow: none;
    font-size: 14px;
    display: block;
}

button.close span {
    display: block;
}
</style>



