<template>
  <div class="new-restaurant wrapper">
<div class="container mt-5 pt-5">
  <h2>Cadastro de Estabelecimento</h2>
  <div class="row">
  <!-- WIDGET-FORM -->
    <div class="col-md-8">
      <div class="widget">
        <div class="widget-body mt-1">
          <b-form>
            <div class="form-group">
              <label for="exampleInputEmail1">Nome *</label>
              <b-form-input v-model="restaurant.name" type="text" placeholder="Restaurante ABC" required></b-form-input>
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Telefone</label>
              <b-btn @click="haveMobileNumber = true" v-if="haveMobileNumber == false" variant="primary" class="float-right mb-1" size="sm"><i class="fa fa-phone mr-1"></i>Não sei o Fixo</b-btn>
              <b-btn @click="haveMobileNumber = false" v-if="haveMobileNumber == true" variant="primary" class="float-right mb-1" size="sm"><i class="fa fa-android mr-1"></i>Não sei o Celular</b-btn>
              <the-mask v-if="haveMobileNumber == false" mask="(##) ####-####" v-model="restaurant.phone" class="form-control" value="" type="tel" placeholder="Fixo"></the-mask>
              <the-mask v-if="haveMobileNumber == true" mask="(##) #####-####" v-model="restaurant.phone" class="form-control" value="" type="tel" placeholder="Celular"></the-mask>
            </div>
            <div class="form-group">
              <label for="Address">Endereço</label>
              <b-form-input id="exampleInput2"
                      type="text"
                      v-model="restaurant.street"
                      placeholder="Nome da Rua">
              </b-form-input>
            </div>
            <div class="form-group">
              <label for="Number">Número</label>
              <b-form-input type="number" v-model="restaurant.number" placeholder="Exemplo: 1100"></b-form-input>
            </div>
            <div class="form-group">
              <label for="Neighborhood">Bairro</label>
              <b-form-input type="text" v-model="restaurant.neighborhood" placeholder="Exemplo: Santa Mônica"></b-form-input>
            </div>
            <div class="form-group">
              <label for="State">Estado</label>
              <b-select v-model="restaurant.city.uf" :options="listStates" size="lg"></b-select>
              <small class="form-text text-muted">Selecione o estado e depois a cidade</small>
            </div>
            <div class="form-group">
              <label for="City">Cidade</label>
              <b-select v-model="restaurant.city" :options="listCities(restaurant.city.uf)" size="lg"></b-select>
            </div>

            <p>
              <b-btn @click="submitRestaurant(restaurant)" class="mt-4" variant="warning" block>
                <i class="fa fa-paper-plane mr-1"></i> Enviar</b-btn>
            </p>
          </b-form>
        </div>
      </div>
    </div>
    <!-- New Restaurant Success Modal -->
  <b-modal ref="NewRestaurantSuccess" title="Sugestão de Restaurante" ok-only ok-variant="primary" @ok="refresh">
    Sacia Fome agradece a indicação do estabelecimento.
  </b-modal>
<!-- ./New Restaurant Success Modal -->
    <!-- ./WIDGET-FORM -->
    <!-- WHY? -->
    <div class="col-md-4">
      <h4>O cadastro é rápido, fácil e gratuito</h4>
      <p>Entraremos em contato o quanto antes 🕘📞</p>
      <hr>
      <img src="http://placehold.it/400x300" alt="" class="img-fluid">
    <!-- ACCORDION -->
    <div role="tablist" class="mt-3">
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-0" role="tab">
          <b-btn block href="#" v-b-toggle.accordion1 variant="warning">Accordion 1</b-btn>
        </b-card-header>
        <b-collapse id="accordion1" accordion="my-accordion1" role="tabpanel">
          <b-card-body>
            <p class="card-text">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam rutrum ut erat a ultricies. Phasellus non auctor
            nisi, id aliquet lectus. Vestibulum libero eros, aliquet at tempus ut, scelerisque sit amet nunc. Vivamus id
            porta neque, in pulvinar ipsum. Vestibulum sit amet quam sem.
            </p>
          </b-card-body>
        </b-collapse>
      </b-card>

      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-0" role="tab">
          <b-btn block href="#" v-b-toggle.accordion2 variant="warning">Accordion 2</b-btn>
        </b-card-header>
        <b-collapse id="accordion2" accordion="my-accordion2" role="tabpanel">
          <b-card-body>
            <p class="card-text">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam rutrum ut erat a ultricies. Phasellus non auctor
            nisi, id aliquet lectus. Vestibulum libero eros, aliquet at tempus ut, scelerisque sit amet nunc. Vivamus id
            porta neque, in pulvinar ipsum. Vestibulum sit amet quam sem.
            </p>
          </b-card-body>
        </b-collapse>
      </b-card>

    </div>
    <!-- ./ACCORDION -->
      <h4 class="m-t-20">Suporte ao Cliente 📨</h4>
      <p> Entre em contato com o nosso suporte.
        <br>Se você tem alguma dúvida, sugestão ou reclamação clique no botão abaixo.</p>
      <p>
        <b-btn block to="fale-conosco" variant="warning" class="mb-2">
          <i class="fa fa-envelope mr-1"></i> Contate-nos</b-btn>
      </p>
    </div>
    <!-- ./WHY? -->
  </div>
</div>
<app-how-get-apps></app-how-get-apps>
</div>

</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import { mapGetters } from 'vuex'
import { TheMask } from 'vue-the-mask'
export default {
  data () {
    return {
      restaurant: {
        name: '',
        city: {name: 'Uberlândia', uf: 'MG'},
        neighborhood: '',
        street: '',
        number: '',
        complement: ''
      },
      haveMobileNumber: false
    }
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    })
  },
  methods: {
    async submitRestaurant (restaurant) {
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer' + localStorage.getItem('token')
      let response = await _axios.post(
        '/lead', {
          name: restaurant.name,
          phone: restaurant.phone,
          street: restaurant.street,
          number: restaurant.number,
          neighborhood: restaurant.neighborhood,
          city: restaurant.city.name,
          state: restaurant.city.uf
        })
      if (response.status === 200) {
        this.$refs.NewRestaurantSuccess.show()
      }
    },
    refresh () {
      location.reload()
    }
  },
  components: {
    AppHowGetApps,
    TheMask
  }
}
</script>

<style scoped>

</style>



