<template>
<b-container class="mt-5 pt-5 mb-5" style="height: 430px;">
  <b-button to="/enderecos" variant="info"><i class="fa fa-arrow-left mr-2"></i>Meus Endereços</b-button>

<div>
        <b-form inline class="text-center" v-if="showCEP">
            <b-container>
                <b-row>
                    <b-col>
                        <the-mask mask="#####-###" class="mb-2 mr-sm-2 mb-sm-0 form-control form-control-lg" type="text" placeholder="Digite seu CEP" v-model="address.cep"></the-mask>
                        <b-button variant="theme" class="btn btn-lg" @click="searchAddress()">Buscar</b-button>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col class="pr-5 mr-5">
                        <b-form-text class="mx-4">
                            <b-link @click="invertSearch()">
                                <u>Não sabe o CEP? Digite seu endereço</u>
                            </b-link>
                        </b-form-text>
                    </b-col>
                </b-row>
            </b-container>
        </b-form>

        <b-form v-if="showAddress" class="search-address">
            <b-container>
                <b-row align-h="center" class="text-center">
                    <b-col cols="12" sm="6">
                        <h3 class="search-address-title">
                            <strong>Informe o endereço de entrega!</strong>
                        </h3>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="2" class="pr-2">
                        <b-select v-model="address.uf" :options="listStates" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </b-col>
                    <b-col cols="12" sm="6" class="pl-0">
                        <b-select v-model="address.city" :options="listCities(address.uf)" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="6" class="pr-2">
                        <b-form-input type="text" placeholder="Digite seu endereço" v-model="address.street" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </b-col>
                    <b-col cols="12" sm="2" class="pl-0">
                        <b-form-input type="text" placeholder="Nº" v-model="address.number" size="lg"></b-form-input>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="6" sm="6" class="pr-2">
                        <b-form-input type="text" placeholder="Complemento" v-model="address.complement" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </b-col>
                    <b-col cols="4" sm="2" class="pl-0">
                        <b-form-input type="text" placeholder="Bairro" v-model="address.neighborhood" size="lg"></b-form-input>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="2" class="pr-2">
                        <b-btn variant="dark" style="width: 100%" size="lg" @click="invertSearch()">Voltar</b-btn>
                    </b-col>
                    <b-col cols="12" sm="6" class="pl-0">
                        <b-btn variant="theme" style="width: 100%" size="lg" @click="saveAddress(address)">Cadastrar Endereço</b-btn>
                    </b-col>
                </b-row>
            </b-container>
        </b-form>
    </div>
  
<!-- New Address Modal -->
    <b-modal id="modalAdd"
                ref="modalAdd"
                size="lg"
                title="Adicionar Endereço"
                title-tag="h4"
                ok-title="Registrar"
                @ok="saveAddress(address)"
                cancel-variant="danger"
                cancel-title="Cancelar">
        <b-form @submit.stop.prevent="handleAddModal">
   <div class="row">
      <div class="form-group col-lg-2">
        <the-mask mask="#####-###" v-model="address.cep" class="form-control" type="text" placeholder="Seu CEP"></the-mask>
      </div>
      <div class="form-group col-lg-8 pl-0">
        <input type="text" v-model="address.street" class="form-control"  placeholder="Nome da rua ou Avenida">
      </div>
      <div class="form-group col-lg-2 pl-0">
        <input type="number" v-model="address.number" class="form-control" placeholder="Nº">
      </div>
    </div>
    <div class="row">
      <div class="form-group col-lg-3">
          <input type="text" v-model="address.complement" class="form-control" placeholder="Casa/Apartamento">
      </div>
      <div class="form-group col-lg-6 pl-0">
        <input type="text" v-model="address.neighborhood" class="form-control" placeholder="Bairro">
      </div>
    </div>
    <div class="row">
      <div class="form-group col-lg-3">
        <label for="UF">Estado</label>
        <b-select v-model="address.city.uf" size="lg" :options="listStates" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
      </div>
      <div class="form-group col-lg-6 pl-0">
        <label for="Cidade">Cidade</label>
        <b-select v-model="address.city" size="lg" :options="listCities(address.city.uf)" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
      </div>
    </div>
        </b-form>
    </b-modal>
  <!-- ./New Address Modal -->

<!-- Modal Success New Address -->
  <b-modal ref="NewAddressSuccess" title="Cadastro de Endereço" ok-only>
    Endereço Cadastrado com Sucesso!
  </b-modal>
<!-- ./Modal Success New Address -->
</b-container>
</template>

<script>
import { TheMask } from 'vue-the-mask'
import { mapGetters, mapActions } from 'vuex'
import GoogleMapsLoader from 'google-maps'

export default {
  data () {
    return {
      disableFields: true,
      showCEP: true,
      showAddress: false,
      address: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        street: '',
        number: ''
      },
      auxAddress: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        neighborhood: '',
        street: '',
        number: '',
        complement: ''
      }
    }
  },
  components: {
    TheMask
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF',
      user: 'user'
    })
  },
  methods: {
    ...mapActions({
      allCities: 'setAllCities',
      allStates: 'setAllStates',
      currentPosition: 'setCurrentPosition'
    }),
    invertFields () {
      this.disableFields = !this.disableFields
    },
    invertSearch () {
      this.invertFields()
      this.showCEP = !this.showCEP
      this.showAddress = !this.showAddress
    },
    async searchAddress () {
      let _axios = this.$http.create()
      let cep = this.address.cep
      let response = await _axios.get(
        'correios/cep/' + cep
      )
      let _address = response.data.data
      this.address = {
        cep: _address.cep,
        uf: _address.uf,
        city: { name: _address.localidade, uf: _address.uf },
        street: _address.logradouro,
        neighborhood: _address.bairro,
        number: ''
      }
      this.invertSearch()
    },
    async saveAddress (address) {
      let response = []
      address = await this.populateCep(address)
      address.users_id = this.user.id
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
      response = await _axios.post(
        'address', address)
      if (response.status === 200) {
        this.$refs.NewAddressSuccess.show()
        this.$router.push('/enderecos')
      }
    },
    populate (address) {
      if (!this.address.cep && !this.address.number) {
        alert('CEP e Número são obrigatórios!')
      } else if (!this.address.number) {
        alert('Número é obrigatório!')
      } else if (!this.address.cep) {
        alert('CEP é obrigatório!')
      } else {
        this.address = JSON.parse(JSON.stringify(address))
        this.$refs.modalAdd.show()
      }
    },
    handleOkAddModal (address) {
      if (!this.address.cep && !this.address.street && !this.address.number && !this.address.neighborhood) {
        alert('Preencha os dados obrigatórios!')
      } else if (!this.address.cep) {
        alert('CEP é obrigatório!')
      } else if (!this.address.street) {
        alert('Nome da Rua ou avenida é obrigatório!')
      } else if (!this.address.number) {
        alert('Número é obrigatório!')
      } else if (!this.address.neighborhood) {
        alert('Bairro é obrigatório!')
      } else {
        this.saveAddress(address)
      }
    },
    getValueByKey (array, key) {
      for (var i = 0; i < array.length; i++) {
        for (var j = 0; j < array[i].types.length; j++) {
          if (array[i].types[j] === key) {
            return array[i].long_name.trim()
          }
        }
      }
      return null
    },
    async populateCep (auxAddress) {
      let strAddress = auxAddress.street + ' ' + auxAddress.number + ' ' + auxAddress.neighborhood + ' ' + auxAddress.city.name + ' ' + auxAddress.city.uf
      let vm = this
      return new Promise(function (resolve, reject) {
        GoogleMapsLoader.KEY = process.env.GOOGLE_MAPS_KEY
        GoogleMapsLoader.load(async function (google) {
          let geocoder = await new google.maps.Geocoder()
          geocoder.geocode({
            'address': strAddress
          }, function (results, status) {
            if (status === 'OK') {
              if (results[0]) {
                let array = results[0].address_components
                let address = {
                  street: vm.getValueByKey(array, 'route'),
                  number: auxAddress.number,
                  complement: auxAddress.complement,
                  neighborhood: vm.getValueByKey(array, 'sublocality'),
                  city: vm.getValueByKey(array, 'administrative_area_level_2'),
                  state: vm.getValueByKey(array, 'administrative_area_level_1'),
                  cep: vm.getValueByKey(array, 'postal_code'),
                  latitude: results[0].geometry.location.lat(),
                  longitude: results[0].geometry.location.lng()
                }
                address.street = address.street ? address.street : auxAddress.address
                address.neighborhood = address.neighborhood ? address.neighborhood : auxAddress.neighborhood

                resolve(address)
              }
            } else {
              reject({street: null})
            }
          })
        })
      })
    }
  },
  created () {
    this.allCities()
    this.allStates()
  }
}
</script>

<style scoped>

</style>



