import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import Home from '@/components/Home'
import Restaurants from '@/components/Restaurants'
import Profile from '@/components/Profile'
import FoodResults from '@/components/FoodResults'
import Checkout from '@/components/Checkout'
import Registration from '@/components/Registration'
import ForgotPassword from '@/components/ForgotPassword'
import Login from '@/components/Login'
import MyAccount from '@/components/partials/user/MyAccount'
import MyOrders from '@/components/partials/user/MyOrders'
import Address from '@/components/partials/user/Address'
import NewAddress from '@/components/partials/user/NewAddress'
import NewRestaurant from '@/components/partials/submit-restaurant/NewRestaurant'
import ContactUs from '@/components/ContactUs'
import Terms from '@/components/root/Terms'

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [{
    path: '/',
    name: 'home',
    component: Home,
    beforeEnter: (to, from, next) => {
      if (store.getters.isLoggedIn) {
        next({name: 'restaurants'})
      } else {
        next()
      }
    }
    // beforeRouteUpdate (to, from, next) {
    //   // react to route changes...
    //   // don't forget to call next()
    // }
  }, {
    path: '/restaurantes',
    name: 'restaurants',
    component: Restaurants
  }, {
    path: '/food-results',
    name: 'foodResults',
    component: FoodResults
  }, {
    path: '/pagamento',
    name: 'checkout',
    component: Checkout,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else if (store.getters.getCartLength === 0) {
        next(false)
      } else {
        next()
      }
    }
  }, {
    path: '/cadastro',
    name: 'registration',
    component: Registration
  }, {
    path: '/entrar',
    name: 'enter',
    component: Login
  }, {
    path: '/esqueci-senha',
    name: 'forgot-password',
    component: ForgotPassword
  }, {
    path: '/fale-conosco',
    name: 'contact-us',
    component: ContactUs
  }, {
    path: '/minha-conta',
    name: 'myaccount',
    component: MyAccount,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/pedidos',
    name: 'myorders',
    component: MyOrders,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/enderecos',
    name: 'address',
    component: Address,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/novo-endereco',
    name: 'newaddress',
    component: NewAddress
  }, {
    path: '/termos',
    name: 'terms',
    component: Terms
  }, {
    path: '/novo-restaurante',
    name: 'newrestaurant',
    component: NewRestaurant
  }, {
    path: '/restaurantes/:id',
    name: 'profile',
    component: Profile,
    props: true
  }
  ]
})



