<template>
<div class="sidebar-wrap">
    <div class="widget widget-cart" v-if="!getCartLength">
        <div class="widget-heading">
            <h3 class="widget-title text-white">
                <i class="fa fa-shopping-cart mr-2 pull-right"></i> Seu Carrinho
            </h3>
            <div class="clearfix"></div>
        </div>
        <b-img center class="mt-5 mb-5" src="../../../assets/img/profile/emot-fome.png"/>
        <!--<b-img center class="mt-5 mb-5" src="../../../assets/img/profile/arrow-cart-add-item.png"/>-->
    </div>
    <div class="widget widget-cart" v-else>
        <div class="widget-heading">
            <h3 class="widget-title text-white">
                <i class="fa fa-shopping-cart mr-2 pull-right"></i> Seu Carrinho ({{getCartLength}})
            </h3>
            <div class="clearfix"></div>
        </div>
        <!-- items (in the cart) container -->
        <div class="order-row" v-for="(item) in cart" :key="item.id">
          <b-img left v-if="item.thumb" rounded :width="75" :height="75" class="m-2 pull-left" :src="item.thumb"/>
          <b-img left v-else rounded :width="75" :height="75" class="m-2 pull-left" src="../../../assets/img/profile/empty-cart.png"/>
            <div class="widget-body">
                <div class="title-row"> {{item.name}}
                    <a @click="removeFromCart(item)">
                        <i style="color: red;" class="fa fa-2x fa-trash pull-right"></i>
                    </a>
                </div>
              <div class="clearfix" v-if="item.options.required.name">
                <div class="form-group row no-gutter">
                  <div class="col-12">
                    <span style="font-size: 15px; font-weight: 700;">
                        <i class="fa fa-exclamation-circle pr-1"></i>{{item.options.required.name}} · R$ {{item.options.required.price}}
                    </span>
                    <hr>
                  </div>                  
                  <div class="col-10 mb-3">
                    <div v-for="(i) in item.options.optional" :key="i.id">
                        <span style="font-size: 14px; font-weight: 300;" v-if="item.options.optional">
                            <i class="fa fa-plus-circle pr-1"></i>{{i.name}} · R$ {{i.price}}
                        </span>
                    </div>                    
                  </div>
                    <div class="col-8">
                        <b-button @click="increment(item)" variant="success" size="sm" class="mr-1"><i class="fa fa-plus"></i></b-button>
                        <span>{{item.quantity}}</span>
                        <b-button @click="decrement(item)" variant="danger" size="sm" class="ml-1"><i class="fa fa-minus"></i></b-button>
                    </div>
                    <div class="col-4 pt-1" v-if="item.options.required.price">
                        <p class="pull-right" style="font-weight: bold; font-size: 18px;">R$ {{item | sumOptionals}}</p>
                    </div>
              </div>
              </div>
          <div class="clearfix" v-else>
                <div class="form-group row no-gutter">
                    <div class="col-8">
                        <b-button @click="increment(item)" variant="success" size="sm" class="mr-1"><i class="fa fa-plus"></i></b-button>
                        <span>{{item.quantity}}</span>
                        <b-button @click="decrement(item)" variant="danger" size="sm" class="ml-1"><i class="fa fa-minus"></i></b-button>
                    </div>
                    <div class="col-4 pt-1">
                        <p class="pull-right" style="font-weight: bold; font-size: 18px;">R$ {{item | sumOptionals}}</p>
                    </div>
              </div>
            </div>
        </div>
      </div>
        <!-- ./items (in the cart) container -->
        <!-- shipping -->
        <div class="widget-delivery clearfix">
            <b-form-radio-group v-model="selected">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col-xs-6 col-sm-12 col-md-6 col-lg-6 b-t-0">
                          <!-- Entregar -->
                          <b-form-radio value="1">
                            <span class="custom-control-description">
                              Entregar
                            </span>
                          </b-form-radio>
                        </div>
                        <div class="col-xs-6 col-sm-12 col-md-6 col-lg-6 b-t-0">
                          <!-- Retirar -->
                          <b-form-radio value="0">
                            <span class="custom-control-description">
                              Retirada
                            </span>
                          </b-form-radio>
                        </div>
                    </div>
                </div>
            </b-form-radio-group>
        </div>
        <!-- ./shipping -->
        <!-- checkout -->
        <div class="widget-body">
          <b-btn class="button-empty" @click="emptyCart"><i class="fa fa-ban pr-1"></i>
            ESVAZIAR CARRINHO
          </b-btn>
          <hr>
            <div class="price-wrap text-sm-center">
              <div v-if="selected == 0">
                <h4 class="pb-2">Endereço para retirada:</h4>
                <div class="takeout-address">
                  {{address.street}} • nº {{address.number}}<br>
                  <div v-if="address.complement">{{address.complement}}<br></div>
                  Bairro: {{address.neighborhood}}<br>
                  {{address.city}} • {{address.state}}
                </div>
                <hr>
                <p>TOTAL</p>
                <h3 class="value pb-3" v-if="subtotal">
                  <strong><vue-numeric read-only currency="R$" separator="." :value="subtotal" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                </h3>
              </div>
              <div v-else>
                <p>SUBTOTAL</p>
                <h3 class="value pb-3">
                  <strong><vue-numeric read-only currency="R$" separator="." :value="subtotal" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                </h3>
                <p>FRETE</p>
                <h3 style="color: green; font-weight: 600;" class="pb-3" v-if="frete && userAddress.street">
                    <vue-numeric read-only currency="R$" separator="." :value="frete" v-bind:precision="2" v-bind:max="10000"></vue-numeric>
                </h3>
                <h5 style="color: red; font-weight: 300;" class="pb-3" v-else>
                    VOCÊ NÃO SELECIONOU UM ENDEREÇO PARA ENTREGA!
                </h5>
                <p v-if="userAddress.street && frete">TOTAL</p>
                <h3 class="value pb-3" v-if="total && userAddress.street">
                    <strong><vue-numeric read-only currency="R$" separator="." :value="total" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                </h3>
              </div>
              <b-btn @click="checkout(selected)" v-if="userAddress.street || selected == 0" class="button-checkout">FECHAR PEDIDO</b-btn>
              <b-btn v-else-if="!userAddress.street && selected == 1" @click="goToAddress()" class="button-address">VER MEUS ENDEREÇOS</b-btn>
            </div>
        </div>
        <!-- ./checkout -->
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      selected: 1
    }
  },
  computed: {
    ...mapGetters({
      userAddress: 'getUserAddress'
    }),
    total () {
      return this.$store.getters.cartTotal
    },
    cart () {
      return this.$store.getters.cartProducts
    },
    getCartLength () {
      return this.$store.getters.getCartLength
    },
    frete () {
      return this.$store.getters.getFrete
    },
    subtotal () {
      return this.$store.getters.cartSubTotal
    },
    address () {
      return this.$store.getters.getRestaurantAddress
    }
  },
  methods: {
    checkout (selected) {
      this.$store.dispatch('setDelivery', selected)
      if (selected === '0') {
        this.$swal({title: 'Entrega do Pedido', text: 'Você confirma que irá buscar o pedido?', type: 'warning', showCancelButton: true, confirmButtonColor: '#3085d6', cancelButtonColor: '#d33', confirmButtonText: 'Sim, irei buscar!', cancelButtonText: 'Cancelar'})
        .then((result) => {
          if (result.value) {
            this.$swal({position: 'center', title: 'Retirada do Pedido', type: 'success', text: 'Você se comprometeu a ir buscar o pedido!', showConfirmButton: false, timer: 1800})
            this.$router.push('/pagamento')
          }
        })
      } else {
        this.$swal({position: 'center', type: 'success', title: 'Finalizar Pedido', showConfirmButton: false, timer: 1500})
        this.$router.push('/pagamento')
      }
    },
    goToAddress () {
      this.$router.push('/enderecos')
    },
    emptyCart () {
      this.$swal({position: 'center', type: 'success', title: 'Você esvaziou o carrinho!', showConfirmButton: false, timer: 1500})
      this.$store.dispatch('emptyCart')
    },
    increment (item) {
      this.$store.dispatch('increment', item)
    },
    decrement (item) {
      this.$store.dispatch('decrement', item)
    },
    removeFromCart (item) {
      this.$swal({position: 'center', type: 'success', title: 'Carrinho', text: 'Você removeu ' + item.name + ' do carrinho!', showConfirmButton: false, timer: 1800})
      this.$store.dispatch('removeFromCart', item)
    }
  },
  filters: {
    sumOptionals (item) {
      let total = 0
      if (item.options.required.price && !item.options.optional.length) {
        total += item.options.required.price * item.quantity
      } else if (item.options.required.price && item.options.optional.length) {
        let optionalsArrayPrice = item.options.optional.map(function (i) {
          return i.price
        })
        let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
        total += (item.options.required.price + optionalsPrice) * item.quantity
      } else {
        total += item.price * item.quantity
      }
      return total
    }
  }
}
</script>

<style scoped>
.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget-heading {
    background-color: #ffb42d;
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.menu-widget .widget-heading {
    border-bottom: none;
}

.widget-heading .widget-title {
    font-size: 16px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.widget-delivery .col-xs-6 {
    padding: 25px 15px 20px;
    background: #fafaf8;
    border-right: 1px solid #eaebeb;
    display: -webkit-inline-box;
    border-bottom: 1px solid #eaebeb;
}

.widget-delivery .col-xs-6:last-of-type {
    border-right: 1px solid transparent;
}

.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 2px;
    position: relative;
}

.widget-body {
    padding: 10px;
}

.order-row .form-control {
    border-radius: 0;
}

.order-row .title-row {
    text-align: justify;
    margin-bottom: 10px;
}

.order-row .title-row a i {
    margin-top: -3px;
}

.order-row {
    border-bottom: 1px solid #eaebeb;
}

.order-row:last-of-type {
    border-bottom: none;
}

@media (max-width: 543px) {
    .widget-cart {
        margin: 20px auto;
        float: right;
        width: 100%;
    }
}

@media (min-width: 544px) and (max-width: 767px) {
    .widget-cart {
        margin: 20px auto;
        float: right;
        width: 66.666667%;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .widget-cart {
        margin-top: 20px;
        float: right;
        width: 66.666667%;
    }
}

.button-empty {
    border: 0 !important;
    color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
    background-color: #138496;
}

.button-checkout {
    border: 0 !important;
    color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
	background-color: #ffb42d;
}

.button-address {
    border: 0 !important;
    color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
    background-color: #0069d9;
}
</style>



