<template>
    <section class="myaccount-page page-wrapper">
        <div class="container">
            <div class="row">
            	<div class="col-md-2"></div>
                <!-- REGISTER -->
                <div class="col-md-8">
                    <div class="widget-body">
                        <span class="title"><i class="fa fa-user-circle-o pr-2"></i> Meu Perfil</span>
                        <div class="headline">Informações Pessoais</div>
                        <form>
                            <div class="row">
                                <div class="form-group col-sm-6">
                                    <label for="exampleInputEmail1">Nome</label>
                                    <input class="form-control input-form" type="text" id="example-text-input" v-model="user.first_name">
                                </div>
                                <div class="form-group col-sm-6">
                                    <label for="exampleInputEmail1">Sobrenome</label>
                                    <input class="form-control input-form" type="text" id="example-text-input-2" v-model="user.last_name">
                                </div>
                                <div class="form-group col-sm-6">
                                    <label for="exampleInputEmail1">Email</label>
                                    <input disabled type="email" class="form-control input-form" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Email" v-model="user.email">
                                    <small id="emailHelp" class="form-text text-muted">Nós nunca compartilharemos seu email com ninguém.</small>
                                </div>
                                <div class="form-group col-sm-6">
                                    <label for="exampleInputEmail1">Telefone</label>

                                    <the-mask class="form-control input-form" v-model="user.phone" masked="true" :mask="['(##) ####-####', '(##) #####-####']" />
                                    <small class="form-text text-muted">Nós nunca compartilharemos seu telefone com ninguém.</small>
                                </div>
                            </div>
                            <div class="row">
                                <b-button class="button-register" @click="update()" >Salvar</b-button>
                            </div>
                        </form>

                        <div class="headline">Senha</div>
                            <form>
                            	<div class="text-entry">Aqui você pode mudar sua senha. Ela deve ter o mínimo de 6 caracteres</div>
                                <div class="row">
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Senha Atual</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword1" v-model="profile.oldPassword">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Nova Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword1" v-model="profile.newPassword">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Confirmar Nova Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword2" v-model="profile.repeatPassword">
                                    </div>
                                </div>
                                <div class="row">
                                    <b-button class="button-register" @click="changePassword()" >Salvar Nova Senha</b-button>
                                </div>
                            </form>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      user: 'user'
    })
  },
  data () {
    return {
      profile: {}
    }
  },
  methods: {
    async update () {
      try {
        this.profile.id = this.user.id
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        let response = await _axios.put(
          '/users', this.user
        )
        if (response.data.status) {
          this.$toasted.success(response.data.message).goAway(3000)
          localStorage.setItem('user', JSON.stringify(this.user))
          this.profile = {}
        } else {
          this.$toasted.error(response.data.message).goAway(3000)
        }
      } catch (e) {
        console.log(e)
      }
    },
    async changePassword () {
      try {
        this.profile.id = this.user.id
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        let response = await _axios.post(
          '/users/password', this.profile
        )
        if (response.data.status) {
          this.$toasted.success(response.data.message).goAway(3000)
          this.profile = {}
        } else {
          this.$toasted.error(response.data.message).goAway(3000)
        }
      } catch (e) {
        console.log(e)
      }
    }
  }
}
</script>

<style scoped>
.widget {
    border: 0px solid #eaebeb;
    background: white;
    border-radius: 3px;
    margin-bottom: 20px;
    position: relative;
}

.widget-body {
    padding: 20px;
}

.title {
    color: #4f4f4f;
    font-weight: 600;
    font-size: 25px;
    padding-bottom: 20px;
}

.input-form {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-register {
    border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 25%;
	background-color: #ffb42d;
	margin-bottom: 5%;
	margin-left: 37%;
}

.headline {
	background: #f2f3f3;
    border: solid 1px #ebebeb;
    box-sizing: border-box;
    color: #7b7b7b;
    display: block;
    font-size: 1.5em;
    font-weight: 700;
    height: 45px;
    line-height: 45px;
    margin-bottom: 10px;
    overflow: hidden;
    padding: 0 20px;
    position: relative;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
}

.text-entry {
	font-size: 18px;
    text-align: center;
    margin-bottom: 20px;
}
</style>




