<template>
<transition name="slide-fade">
    <div>
        <section class="popular" v-if="popularRestaurants.length && lastUsed.city == 'Uberlândia'">
            <b-container>
                <div class="title text-sm-center m-b-30">
                    <h2>Populares este mês em <span class="primary-color location-result">{{lastUsed.city}}</span> 🎉</h2>
                    <p class="lead">A maneira mais fácil de encontrar sua comida favorita</p>
                </div>
                <b-row>
                <div class="col-xs-12 col-sm-6 col-md-12 food-item">
                    <carousel :navigationEnabled="true" :loop="true" :autoplay="false" :perPageCustom="[[320,1], [375, 1], [768, 3], [1024, 4]]">
                        <slide v-for="(restaurant) in popularRestaurants" :key="restaurant.id">
                            <div class="col-xs-12 col-sm-6 col-md-12 food-item">
                            <b-badge variant="transparent" class="open-ribbon" v-if="restaurant.open == 1"><span>ABERTO</span></b-badge>
                            <b-badge variant="transparent" class="closed-ribbon" v-else-if="restaurant.open == 0"><span>FECHADO</span></b-badge>
                                <div class="food-item-wrap">
                                    <div class="figure-wrap">
                                        <router-link :to="'/restaurantes/' + restaurant.id">
                                            <b-img rounded :src="restaurant.img" alt="Restaurant logo" style="width: 100%" height="210"/>
                                        </router-link>
                                    </div>
                                    <div class="content">
                                       <router-link :to="'/restaurantes/' + restaurant.id"><h4 class="new" style="font-size: 22px;">{{restaurant.name}}</h4></router-link>
                                        <span style="font-size: 14px;">{{restaurant.Categories | category}}</span>
                                        <div class="rating mt-2 mb-2" v-if="restaurant.stars != null">
                                            <star-rating v-model="restaurant.stars" :inline="true" :star-size="15" :read-only="true" :increment="0.01" :fixed-points="2" :show-rating="false"></star-rating>
                                        </div>
                                        <div v-else class="mt-2 mb-2">
                                            <span style="color: #ffb42d;">NOVO! <i class="fa fa-fire"></i></span>
                                        </div>
                                        <!--<div v-if="restaurant.amount_reviews != null">
                                            <a>{{restaurant.amount_reviews}} avaliações</a>
                                        </div>
                                        <div v-else-if="restaurant.amount_reviews == null">
                                            <p class="new text-left mt-2"><strong>NOVO!</strong> <i class="fa fa-fire"></i></p>
                                        </div>-->
                                        <div>
                                        <div class="distance">
                                            <i class="fa fa-location-arrow mr-1"></i>{{restaurant.distance}} km
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </slide>
                    </carousel>
                </div>
                </b-row>
            </b-container>
        </section>
        <section class="popular" v-else>
            <b-container>
                <div class="title text-sm-center m-b-30">
                    <h2>A cidade de <span class="primary-color location-result">{{lastUsed.city}}</span> ainda não está disponível</h2>
                    <p class="lead">Não se preocupe, estamos em constante expansão! 😉</p>
                </div>
            </b-container>
        </section>
    </div>
    </transition>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      popularRestaurants: []
    }
  },
  computed: {
    ...mapGetters({
      lastUsed: 'lastUsed',
      currentPosition: 'currentPosition'
    })
  },
  methods: {
    async getPopularRestaurants () {
      let response = []
      try {
        let _city = this.lastUsed.city ? this.lastUsed.city : 'Uberlândia'
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/city/' + _city + '/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng + '/limit/30'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  filters: {
    category (categories) {
      return categories.map(category => category.name).join(' · ')
    }
  },
  created () {
    this.getPopularRestaurants().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response) => {
      this.popularRestaurants = response
    })
  }
}
</script>

<style scoped>
/* ==========================
*  Popular block
============================*/

.popular {
    padding: 35px 0 30px;
    background-size: 100%;
}

.categories {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 14px;
    font-weight: 400;
}

/* RIBBON BADGES */

    /* OPEN RESTAURANT */

.open-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

    /* ./OPEN RESTAURANT */

    /* CLOSED RESTAURANT */

.closed-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.closed-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: linear-gradient(#F70505 0%, #B22222 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -31px;
}
.closed-ribbon span::before {
  content: "";
  position: absolute; left: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid #B22222;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}
.closed-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #B22222;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}

 /* ./RIBBON BADGES */

.slide-footer {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 14px;
    font-weight: 700;
}

.food-item-wrap {
    border: 1px solid #eaebeb;
    border-radius: 2px;
    overflow: hidden;
    margin-bottom: 30px;
    background: #fafaf8;
}

.food-item-wrap h5 a {
    color: #25282b;
    font-size: 21px;
    font-weight: 600;
}

.food-item-wrap .title h2 {
    font-size: 16px;
    margin-bottom: 35px;
    color: #414551;
    line-height: 21px;
    font-weight: 300;
}

.food-item-wrap .price {
    font-size: 21px;
    font-weight: 700;
    color: #000;
    margin-top: 4px;
    display: inline-block;
}

.food-item-wrap .product-name {
    margin-bottom: 20px;
}

.food-item-wrap .content {
    padding: 25px 15px 20px;
}

.food-item-wrap .restaurant-block {
    border-top: 1px solid #eaebeb;
    float: left;
    width: 100%;
}

.food-item-wrap .right-text {
    margin-left: 10px;
}

.food-item-wrap .right-text a {
    display: block;
}

.food-item-wrap .left {
    float: left;
    padding: 8px 15px;
}

.food-item-wrap .left img {
    padding-top: 3px;
}

@media (min-width: 320px) and (max-width: 568px) {
    .food-item-wrap .left {
        float: left;
        padding: 10px;
    }
    .food-item .left img {
        width: 45px
    }
    .food-item-wrap .right-text {
        font-size: 13px;
    }
}

.food-item-wrap .right {
    padding: 10px 0;
}

.food-item-wrap .right-like-part {
    border-left: 1px solid #eaebeb;
    padding: 25px 15px;
    font-size: 13px;
}

@media (min-width: 280px) and (max-width: 580px) {
    .food-item-wrap .right-like-part {
        padding: 24px 10px;
        font-size: 10px;
    }
}

.food-item-wrap .right-like-part img {
    vertical-align: middle;
    margin-right: 5px;
}

.food-item-wrap .right-text a {
    color: #25282b;
    font-weight: 400;
}

.food-item-wrap .right-text span,
.food-item-wrap .right-like-part,
.food-item-wrap .right-like-part span {
    font-weight: 300;
    color: #748796;
}

.food-item-wrap .figure-wrap {
    position: relative;
    height: 210px;
}

.food-item-wrap .food-item-wrap figure {
    position: relative;
    overflow: hidden;
    margin-bottom: 15px;
}

.food-item-wrap .food-item-wrap figure img {
    width: 100%;
}

.food-item-wrap .figure-wrap:after {
    position: absolute;
    bottom: -1px;
    left: 0;
    content: "";
    background: url(../../../assets/img/home/zig-zag.png);
    width: 100%;
    height: 5px;
}

.food-item-wrap:hover h5>a,
.food-item-wrap:hover .right-text>a {
    color: #ffb42d;
}

.food-item-wrap .figure-text {
    position: absolute;
    top: 0;
    padding: 13px 28px 18px 18px;
    width: 100%;
    height: 100%;
}

.food-item-wrap .figure-text .bottom {
    position: absolute;
    bottom: 25px;
    float: left;
    width: 87%;
}

.food-item-wrap .review {
    margin-top: 5px;
    font-size: 10px;
    text-transform: uppercase;
}

.food-item-wrap .distance {
    background: #ffb42d;
    border-radius: 3px;
    color: black;
    font-size: 14px;
    padding: 0 10px;
    display: inline-block;
}

.food-item-wrap .rating i {
    color: #ffd953;
    font-size: 16px;
}

.food-item-wrap .rating .fa-star-o {
    font-size: 17px;
}

.food-item-wrap .review {
    right: 30px;
    position: absolute;
    bottom: 30px;
}

.food-item-wrap .review a {
    color: #fff;
}

.food-item-wrap .left-sidebar,
.food-item-wrap .right-sidebar {
    width: 300px;
}

.food-item-wrap .lf-ghost {
    width: 395px!important;
    float: left;
}

@media (min-width: 544px) and (max-width: 767px) {
    .food-item-wrap .right-text {
        margin: 5px 10px;
        font-size: 14px;
        line-height: 20px;
    }
    .food-item-wrap .right-like-part {
        width: 100%;
        padding: 5px 10px;
        background: #fff;
        border: 0;
        border-top: 1px solid #eaebeb;
        text-align: center;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .food-item-wrap .restaurant-block {
        text-align: center;
    }
    .food-item-wrap .left {
        float: none
    }
    .food-item-wrap .pull-left {
        float: none
    }
    .food-item-wrap .right-text span,
    .food-item-wrap .right-like-part,
    .food-item-wrap .right-like-part span {
        width: 100%;
    }
    .right-like-part.pull-right {
        background-color: #fff;
        border: 0;
        border-top: 1px solid #eaebeb;
        padding: 10px;
    }
    .food-item-wrap .price-btn-block {
        text-align: center
    }
    .food-item-wrap .price-btn-block .price {
        margin-bottom: 15px;
        display: block;
    }
    .food-item-wrap .price-btn-block .btn {
        float: none;
    }
}

@media (min-width: 992px) and (max-width: 1199px) {
    .food-item-wrap .left {
        padding: 10px;
        font-size: 11px;
        line-height: 20px;
    }
}

.slide-fade-enter-active {
  transition: all .4s ease;
}
.slide-fade-leave-active {
  transition: all .6s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active em versões anteriores a 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>


