<template>
    <!-- location match part starts -->
    <div class="location-match text-sm-center">
        <div class="container">
            <span v-if="totalRestaurants > 0 && lastUsed.city == 'Uberlândia'"><span id="location-match-count">{{totalRestaurants}}</span> restaurantes correspondentes à sua localização:
                <span class="primary-color location-result">{{lastUsed.city}}</span>
            </span>
            <span v-else>Não há estabelecimentos correspondentes à sua localização 😓</span>
        </div>
    </div>
    <!-- location match part ends -->
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      totalRestaurants: 0
    }
  },
  computed: {
    ...mapGetters({
      lastUsed: 'lastUsed',
      currentPosition: 'currentPosition'
    })
  },
  methods: {
    async getPopularRestaurants () {
      let response = []
      try {
        let _city = this.lastUsed.city ? this.lastUsed.city : 'Uberlândia'
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/city/' + _city + '/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng + '/limit/30'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  created () {
    this.getPopularRestaurants().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response) => {
      this.totalRestaurants = response.length
    })
  }
}
</script>

<style scoped>
/* ==========================
*   Detect location
============================*/

.location-match {
    padding: 20px 0;
    border-bottom: 1px solid #e6e6e6;
}

.location-match span {
    font-weight: 600;
}

.location-match a {
    color: #ffb42d;
}

.location-match a:hover {
    color: #43434c;
}
</style>



