// import * as types from '../mutation-types'

const state = { // equivalente ao data ()
  cuisines: [],
  items: [],
  cart: {
    flag_delivery: 1,
    status: 1,
    frete: 0.0,
    discount: {
      status: false,
      name: ''
    },
    subTotal: 0.0,
    total: 0.0,
    users_id: null,
    push_user_id: null, // OneSignal
    restaurants_id: null,
    orders: {
      user: {},
      restaurant: {
        address: {}
      },
      items: [],
      address: {},
      payment: {}
    },
    promotion: {},
    money: null,
    origin: 'web'
  }
}

const getters = { // funciona como um get (equivalente ao computed)
  getCart (state) {
    return state.cart
  },
  getCartItems (state) {
    return state.cart.orders.items
  },
  getFrete (state) {
    return state.cart.frete
  },
  getDelivery (state) {
    return state.cart.flag_delivery
  },
  getCartLength (state) {
    return state.cart.orders.items.length
  },
  getRestaurant (state) {
    return state.cart.orders.restaurant
  },
  getUserAddress (state) {
    return state.cart.orders.address
  },
  getRestaurantAddress (state) {
    return state.cart.orders.restaurant.address
  },
  cartProducts (state) {
    return state.cart.orders.items.map(cartItem => {
      const product = state.cart.orders.items.find(product => product.id === cartItem.id)
      return {
        id: product.id,
        name: product.name,
        price: product.price,
        options: product.options,
        thumb: product.thumb,
        quantity: cartItem.quantity
      }
    })
  },
  cartSubTotal (state) {
    // subtotal = sem valor do frete
    return state.cart.subTotal
  },
  cartTotal (state) {
    let total = state.cart.subTotal
    total += state.cart.frete
    return total
  },
  getDiscount (state) {
    return state.cart.discount
  }
}

const actions = {
  addProductToCart (context, payload) {
    const cartItem = context.state.cart.orders.items.find(item => item.id === payload.id)
    if (!cartItem) {
      context.commit('pushProductToCart', payload)
      context.commit('calculateSubTotal')
    } else {
      context.commit('incrementItemQuantity', cartItem)
      context.commit('calculateSubTotal')
    }
  },
  increment (context, item) {
    context.commit('incrementQuantity', item)
    context.commit('calculateSubTotal')
  },
  decrement (context, item) {
    context.commit('decrementQuantity', item)
    context.commit('calculateSubTotal')
  },
  removeFromCart (context, item) {
    context.commit('removeFromCart', item)
  },
  emptyCart (context) {
    context.commit('emptyCart')
    context.commit('calculateSubTotal')
  },
  setDelivery (context, payload) {
    context.commit('setFlagDelivery', payload)
  },
  setAddress (context, payload) {
    context.commit('setShippingAddress', payload)
  },
  setUser (context, payload) {
    context.commit('setUser', payload)
  },
  setRestaurant (context, payload) {
    context.commit('setRestaurant', payload)
  },
  setPayment (context, payload) {
    context.commit('setPayment', payload)
  },
  setPaymentCash (context, payload) {
    context.commit('setPaymentCash', payload)
  },
  setTotal (context, payload) {
    context.commit('setTotal', payload)
  }
}

const mutations = { // alterar estado
  setTotal (state, payload) {
    state.cart.total = payload
  },
  setPaymentCash (state, payload) {
    state.cart.money = payload
  },
  setPayment (state, payload) {
    state.cart.orders.payment = payload
  },
  setItems (state, payload) {
    state.items = payload
  },
  setFlagDelivery (state, payload) {
    state.cart.flag_delivery = payload
  },
  incrementQuantity (state, item) {
    for (var i in state.cart.orders.items) {
      if (state.cart.orders.items[i].id === item.id) {
        state.cart.orders.items[i].quantity++
        break
      }
    }
  },
  decrementQuantity (state, item) {
    for (var i in state.cart.orders.items) {
      if (state.cart.orders.items[i].id === item.id) {
        if (item.quantity > 1) {
          state.cart.orders.items[i].quantity--
          break
        } else {
          state.cart.orders.items.splice(i, 1)
          break
        }
      }
    }
  },
  setCuisines (state, payload) {
    state.cuisines = payload
  },
  setFrete (state, payload) {
    state.cart.frete = payload.price
  },
  removeFromCart (state, item) {
    for (let i in state.cart.orders.items) {
      if (state.cart.orders.items[i].id === item.id) {
        state.cart.orders.items.splice(i, 1)
        break
      }
    }
    let subtotal = 0
    let itemsInCart = state.cart.orders.items
    itemsInCart.forEach(item => {
      if (item.options.required.price && !item.options.optional.length) {
        subtotal += item.options.required.price * item.quantity
      } else if (item.options.required.price && item.options.optional.length) {
        let optionalsArrayPrice = item.options.optional.map(function (i) {
          return i.price
        })
        let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
        subtotal += (item.options.required.price + optionalsPrice) * item.quantity
      } else {
        subtotal += item.price * item.quantity
      }
    })
    state.cart.subTotal = subtotal
  },
  calculateSubTotal (state) {
    let subtotal = 0
    let itemsInCart = state.cart.orders.items
    itemsInCart.forEach(item => {
      if (item.options.required.price && !item.options.optional.length) {
        subtotal += item.options.required.price * item.quantity
      } else if (item.options.required.price && item.options.optional.length) {
        let optionalsArrayPrice = item.options.optional.map(function (i) {
          return i.price
        })
        let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
        subtotal += (item.options.required.price + optionalsPrice) * item.quantity
      } else {
        subtotal += item.price * item.quantity
      }
    })
    state.cart.subTotal = subtotal
    let total = subtotal
    let delivery = state.cart.flag_delivery
    let frete = state.cart.frete
    if (delivery === 1) {
      total += frete
    } else {
      state.cart.total = subtotal
    }
    state.cart.total = total
  },
  pushProductToCart (state, payload) {
    let subtotal = 0
    if (payload.options.required.price && !payload.options.optional.length) {
      subtotal += payload.options.required.price
    } else if (payload.options.required.price && payload.options.optional.length) {
      let optionalsArrayPrice = payload.options.optional.map(function (i) {
        return i.price
      })
      let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
      subtotal += (payload.options.required.price + optionalsPrice)
    } else {
      subtotal += payload.price
    }
    state.cart.orders.items.push({
      id: payload.id,
      name: payload.name,
      price: payload.price,
      cuisines_id: payload.cuisines_id,
      restaurants_id: payload.restaurants_id,
      thumb: payload.thumb,
      options: payload.options,
      ingredients: payload.ingredients,
      amount: payload.amount,
      disponible: payload.disponible,
      max_per_item: payload.max_per_item,
      valid_days: payload.valid_days,
      total: subtotal,
      quantity: 1
    })
  },
  incrementItemQuantity (state, cartItem) {
    cartItem.quantity++
  },
  emptyCart (state) {
    state.cart.orders.items.length = 0
    state.cart.orders.items = []
    state.cart.total = 0
    state.cart.subTotal = 0
  },
  setUser (state, payload) {
    state.cart.orders.user.id = payload.id
    state.cart.users_id = payload.id
    state.cart.orders.user.img = payload.img
    state.cart.orders.user.first_name = payload.first_name
    state.cart.orders.user.last_name = payload.last_name
    state.cart.orders.user.email = payload.email
    state.cart.orders.user.cpf = payload.cpf
    state.cart.orders.user.country_code = payload.country_code
    state.cart.orders.user.phone = payload.phone
    state.cart.orders.user.isNew = payload.isNew
  },
  setShippingAddress (state, payload) {
    state.cart.orders.address.street = payload.street
    state.cart.orders.address.number = payload.number
    state.cart.orders.address.complement = payload.complement
    state.cart.orders.address.neighborhood = payload.neighborhood
    state.cart.orders.address.city = payload.city
    state.cart.orders.address.state = payload.state
    state.cart.orders.address.cep = payload.cep
    state.cart.orders.address.latitude = payload.latitude
    state.cart.orders.address.longitude = payload.longitude
  },
  setRestaurant (state, payload) {
    state.cart.orders.restaurant.percentage_discount = payload.percentage_discount
    state.cart.orders.restaurant.id = payload.id
    state.cart.restaurants_id = payload.id
    state.cart.orders.restaurant.name = payload.name
    state.cart.orders.restaurant.img = payload.img
    state.cart.orders.restaurant.latitude = payload.latitude
    state.cart.orders.restaurant.longitude = payload.longitude
    state.cart.orders.restaurant.delivery_min = payload.delivery_min
    state.cart.orders.restaurant.delivery_max = payload.delivery_max
    state.cart.orders.restaurant.frete = payload.frete
    state.cart.orders.restaurant.frete_km = payload.frete_km
    state.cart.orders.restaurant.km_max = payload.km_max
    state.cart.orders.restaurant.km_normal = payload.km_normal
    state.cart.orders.restaurant.payment = payload.Payments
    state.cart.orders.restaurant.phone = payload.phone
    state.cart.orders.restaurant.distance = payload.distance
    state.cart.orders.restaurant.delivery_pickup = payload.delivery_pickup
    state.cart.orders.restaurant.address.street = payload.street
    state.cart.orders.restaurant.address.number = payload.number
    state.cart.orders.restaurant.address.complement = payload.complement
    state.cart.orders.restaurant.address.neighborhood = payload.neighborhood
    state.cart.orders.restaurant.address.city = payload.city
    state.cart.orders.restaurant.address.state = payload.state
    state.cart.orders.restaurant.address.cep = payload.cep
    state.cart.orders.restaurant.address.latitude = payload.latitude
    state.cart.orders.restaurant.address.longitude = payload.longitude
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



