<template>
   <div v-if="cuisines && cuisines.length">
  <div v-for="(i) in cuisines" :key="i.id">
<div class="menu-widget" id="2" v-if="i.Items && i.Items.length">
    <div class="widget-heading">
        <h3 class="widget-title text-dark" v-b-toggle="'collapse' + i.id">
            {{ i.name | uppercase }}
            <b-link v-b-toggle="'collapse' + i.id" class="btn btn-link pull-right">
                <span v-b-toggle="'collapse' + i.id" class="fa fa-angle-right arrow pull-right"></span>
                <i v-b-toggle="'collapse' + i.id" class="fa fa-angle-down arrow pull-right"></i>
            </b-link>
        </h3>
        <div class="clearfix"></div>
    </div>
    <b-collapse :id="'collapse' + i.id">
        <div class="food-item" v-for="(item) in items" :key="item.id" v-if="item.cuisines_id == i.id">
            <b-row>
                <div class="col-xs-12 col-sm-12 col-lg-8">
                    <div class="rest-logo pull-left" v-if="item.thumb">
                        <a class="restaurant-logo pull-left">
                          <!-- Food Logo 100x80 -->
                            <img :src="item.thumb" :width="100" :height="80" :alt="item.name">
                        </a>
                    </div>
                    <div class="rest-logo pull-left" v-else>
                        <a class="restaurant-logo pull-left">
                          <!-- Food Logo 100x80 -->
                            <b-img src="../../../assets/img/profile/empty-cart.png" :width="100" :height="80" :alt="item.name"/>
                        </a>
                    </div>
                    <!-- end:Logo -->
                    <div class="rest-descr">
                        <h6>
                            <a>{{ item.name }}</a>
                        </h6>
                        <p>{{ item.ingredients }}</p>

                    </div>
                    <!-- end:Description -->
                </div>
                <!-- end:col -->
                <div class="col-xs-12 col-sm-12 col-lg-4 pull-right item-cart-info" v-if="item.disponible == true">
                    <span class="price pull-left">R$ {{ item.price }}</span>
                    <div v-if="(item.options.required && item.options.required.length) || (item.options.optional && item.options.optional.length)">
                  <b-btn @click="populate(item)" variant="success" class="pull-right"><i class="fa fa-2x fa-cart-plus"></i></b-btn>
                  </div>
                  <div v-else>
                    <b-btn @click="addProductToCart(item)" variant="success" class="pull-right"><i class="fa fa-2x fa-cart-plus"></i></b-btn>
                  </div>
                    <!--<b-link v-b-modal.order-modal class="btn btn-small btn btn-success pull-right"><i class="fa fa-2x fa-cart-plus"></i></b-link>-->
                </div>
                <div class="col-xs-12 col-sm-12 col-lg-4 pull-right item-cart-info" v-else>
                    <span class="price pull-left">Indisponível</span>
                    <b-btn disabled variant="danger" class="pull-right"><i class="fa fa-2x fa-cart-plus"></i></b-btn>
                </div>
            </b-row>
            <!-- end:row -->
        </div>
        <!-- end:Food item -->
    </b-collapse>
    <!-- end:Collapse -->
  </div>
</div>
<!-- Modal -->
<b-modal id="order-modal" ref="orderModal" title="Escolha uma opção (obrigatório)" title-tag="h4" size="lg">
    <b-container fluid class="cart-addon">
      <!-- body -->
        <div class="food-item white" v-for="(required) in productRequired" :key="required.id" v-if="productRequired && productRequired.length">
            <b-row>
              <div class="col-xs-12 col-sm-12 col-lg-8">
                        <div class="item-img pull-left">
                            <b-form-radio-group v-model="RequiredSelected" name="Obrigatórios">
                              <b-form-radio :value="required">
                                <div class="rest-descr">
                                    <h6>
                                      {{required.name}}
                                    </h6>
                                </div>
                                </b-form-radio>
                          </b-form-radio-group>
                        </div>

                    </div>
                    <div class="col-xs-12 col-sm-12 col-lg-4 pull-right item-cart-info">
                        <span class="price pull-right">R$ {{required.price}}</span>
                    </div>
              </b-row>
            </div>
            <h4 v-if="productOptional.length" class="pb-3">Adicionais ({{productOptional.length}} opções)</h4>
              <div class="food-item white" v-for="(additional) in productOptional" :key="additional.id" v-if="productOptional && productOptional.length">
                <b-row>
                  <div class="col-xs-12 col-sm-12 col-lg-8">
                            <div class="item-img pull-left">
                              <b-form-checkbox-group v-model="AdditionalSelected" name="Adicionais">
                                <b-form-checkbox :value="additional">
                                <div class="rest-descr">
                                    <h6>
                                        {{additional.name}}
                                    </h6>
                                </div>
                              </b-form-checkbox>
                              </b-form-checkbox-group>
                            </div>
                        </div>
                        <div class="col-xs-12 col-sm-12 col-lg-4 pull-right item-cart-info">
                            <span class="price pull-right">R$ {{additional.price}}</span>
                        </div>
                </b-row>
              </div>
        <!-- ./body -->
    </b-container>
    <div slot="modal-footer">
        <b-btn variant="outline-primary" @click="hideModal">Fechar</b-btn>
        <b-btn @click="addProductToCart(item)" variant="theme"><i class="fa fa-shopping-cart mr-2"></i>Adicionar ao carrinho</b-btn>
    </div>
</b-modal>
<!-- ./Modal -->
</div>
<!-- end:Widget menu -->
</template>

<script>
export default {
  props: ['cuisines', 'items'],
  data () {
    return {
      item: {},
      productOptional: [],
      productRequired: [],
      RequiredSelected: {},
      AdditionalSelected: []
    }
  },
  methods: {
    addProductToCart (item) {
      let newItem = JSON.parse(JSON.stringify(item))
      newItem.options.required = this.RequiredSelected
      newItem.options.optional = this.AdditionalSelected
      if (!item.options.required.length && !item.options.required.price) {
        this.$store.dispatch('addProductToCart', newItem)
        this.$swal({position: 'center', type: 'success', title: 'Item adicionado ao carrinho! 🛒', showConfirmButton: false, timer: 1500})
        this.AdditionalSelected = []
        this.RequiredSelected = {}
      } else if (item.options.required.length && newItem.options.required.price) {
        this.$store.dispatch('addProductToCart', newItem)
        this.$swal({position: 'center', type: 'success', title: 'Item adicionado ao carrinho! 🛒', showConfirmButton: false, timer: 1500})
        this.AdditionalSelected = []
        this.RequiredSelected = {}
        this.$root.$emit('bv::hide::modal', 'order-modal')
      } else {
        this.$swal({type: 'error', title: 'Oops...', text: 'Escolha um obrigatório!'})
        this.AdditionalSelected = []
        this.RequiredSelected = {}
      }
    },
    populate (item) {
      this.productOptional = JSON.parse(JSON.stringify(item.options.optional))
      this.productRequired = JSON.parse(JSON.stringify(item.options.required))
      this.item = item
      this.$refs.orderModal.show()
    },
    hideModal () {
      this.AdditionalSelected = []
      this.RequiredSelected = {}
      this.$root.$emit('bv::hide::modal', 'order-modal')
    }
  },
  filters: {
    uppercase: function (value) {
      return value.toUpperCase()
    }
  }
}
</script>

<style scoped>
.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.menu-widget .widget-heading {
    border-bottom: none;
    margin-bottom: 10px;
}

.widget-heading .widget-title {
    font-size: 20px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;

    color: #7b7b7b;

    font-weight: 400;

    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.widget-heading .widget-title .btn {
    padding: 0px 24px;
}

.menu-widget,
.widget {
    border: 1px solid #eaebeb;
    background: #f2f3f3;
    border-radius: 2px;
    position: relative;
    margin-bottom: 20px;
    cursor: pointer;
}

.menu-widget .food-item {
    padding: 15px;
        background: white;
}

.menu-widget .food-item:first-of-type {
    border-bottom: none;
}

.menu-widget .food-item .restaurant-logo {
    max-width: 100px;
    height: auto;
    display: block;
}

.menu-widget .food-item .restaurant-logo img {
    max-width: 100%;
    display: block;
}

.menu-widget .food-item:first-of-type {
    border-top: 1px solid #eaebeb;
}

.menu-widget .food-item:last-of-type {
    border-bottom: transparent;
}

.menu-widget .food-item.white {
    background-color: #fff;
}

.menu-widget .food-item .rest-descr {
    padding-left: 115px;
    padding-top: 0;
}

.menu-widget .food-item .rest-descr h6 a {
    color: #25282b;
    font-size: 18px;
    font-weight: 600;
}

.menu-widget .food-item .item-cart-info {
    padding-top: 20px;
}

.menu-widget .food-item .price {
    font-weight: 600;
    color: #000;
    position: relative;
    margin-top: 6px;
}

.arrow {
    font-size: 44px;
    position: absolute;
    margin-left: 0px;
    margin-top: -7px;
    color: #ffb42d;
}

a[aria-expanded=true] .fa-angle-right {
    display: none;
}

a[aria-expanded=false] .fa-angle-down {
    display: none;
}
/*============================================
* Modal
=============================================*/

.btn {
    cursor: pointer;
}

.cart-addon .food-item {
    margin-bottom: 20px;
    border-bottom: 1px solid #ededed;
    padding: 2px 0 15px;
}

.cart-addon .food-item:last-of-type {
    border-bottom: none;
}

.cart-addon .food-item .item-img img {
    max-width: 70px;
}

.cart-addon .food-item .rest-descr {
    margin-left: 40px;
}

.cart-addon .food-item h6 a {
    font-size: 16px;
    color: #4d4f56;
    display: block;
    margin: 4px 0 0 0;
    line-height: 20px;
}

.cart-addon .food-item .price {
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 2px;
    display: block;
    color: #25282b;
}

.cart-addon .food-item .form-control {
    margin-top: 8px;
}

button.close {
    padding: 0;
    cursor: pointer;
    background: 0 0;
    border: 0;
    -webkit-appearance: none;
    right: -15px;
    position: absolute;
    top: -15px;
    background-color: #ffb42d;
    opacity: 1;
    width: 30px;
    height: 30px;
    color: #fff;
    text-shadow: none;
    font-size: 14px;
    display: block;
}

button.close span {
    display: block;
}
</style>



