            <a v-b-toggle="'collapse' + item.id">{{ item.name }}</a>
          </li>
        </ul>
        <div class="clearfix"></div>
    </div>
    <!-- end:Sidebar nav -->
</div>
</template>

<script>
export default {
  props: ['cuisines'],
  data () {
    return {}
  }
}
</script>

<style scoped>
.sidebar {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
}

.sidebar-inner {
    padding-left: 15px;
    padding-right: 15px;
}

.sidebar-inner .sidebar input,
.sidebar .input-group-btn .btn {
    height: 55px;
    border-left: 0;
    border-right: 0;
}

.widget-delivery .col-xs-6 {
    padding: 25px 15px 20px;
    background: #fff;
    border-right: 1px solid #eaebeb;
    display: -webkit-inline-box;
