import Vue from 'vue'
import * as types from '../mutation-types'

// You can use it as state property
const state = {
  all: []
}

// You can use it as a state getter function (probably the best solution)
const getters = {
  allPayments: state => {
    return state.all
  }
}

// Mutation for when you use it as state property
const mutations = {
  [types.RECEIVE_PAYMENTS] (state, payload) {
    state.all = payload.payments
  },
  [types.PAYMENT_ISNOTVALID] (state, payload) {
    // state.all
    const payment = state.all.find(p => p.id === payload.id)
    payment.isValid = false
  }
}

const actions = {
  getAllPayments: ({
    commit
  }) => {
    let _axios = Vue.axios.create()
    _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
    _axios.get('/payment').then((response) => {
      return response.data.data
    }).then(response => {
      if (response) {
        return response.map(payment => {
          payment.isValid = true
          if (payment.card) {
            payment.title = payment.name + ' ' + payment.card
          }
          return payment
        })
      }
      return []
    }).then(response => {
      commit(types.RECEIVE_PAYMENTS, {
        payments: response
      })
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



