<template>
<b-nav-item-dropdown id="logged-dropdown" right>
    <!-- Using button-content slot -->
    <template slot="button-content">
        <em>Bem-vindo(a), <b>{{ user.first_name }}</b></em>
    </template>
    <b-dropdown-item to="/minha-conta"><i class="fa fa-user-circle-o pr-2"></i>Perfil</b-dropdown-item>
    <b-dropdown-item disabled href="#"><i class="fa fa-money pr-2"></i>Carteira Digital</b-dropdown-item>
    <b-dropdown-item to="/enderecos"><i class="fa fa-map pr-2"></i>Meus endereços</b-dropdown-item>
    <b-dropdown-item disabled href="#"><i class="fa fa-heart pr-2"></i>Favoritos</b-dropdown-item>
    <b-dropdown-item to="/pedidos"><i class="fa fa-th-list pr-2"></i>Meus pedidos</b-dropdown-item>
    <b-dropdown-item to="/fale-conosco"><i class="fa fa-comments pr-2"></i>Fale Conosco</b-dropdown-item>
    <b-dropdown-item @click="logout()"><i class="fa fa-sign-out pr-2"></i>Sair</b-dropdown-item>
</b-nav-item-dropdown>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF',
      user: 'user'
    })
  },
  methods: {
    logout () {
      this.$store.dispatch('logout').then(() => {
        this.$router.push({name: 'home'})
      })
    }
  }
}
</script>

<style scoped>
</style>



