<template>
  <div class="checkout wrapper">
  <b-container class="mt-3 cart-addon">
    <div class="text-center mb-3">
        <h2>{{restaurant.name | Upper}}</h2>
      <span v-if="delivery == 1"><i class="fa fa-motorcycle pr-2"></i>Tempo de entrega: {{restaurant.delivery_min}} • {{restaurant.delivery_max}}</span>
      <span v-else><i class="fa fa-clock-o pr-2"></i>Tempo de espera: {{restaurant.delivery_min}} • {{restaurant.delivery_max}}</span>
    </div>
        <div class="widget clearfix">
            <div class="widget-heading">
                <h3 class="widget-title text-dark">
                    <i class="fa fa-list pr-2"></i>DETALHES DO PEDIDO
                </h3>
                <div class="clearfix"></div>
            </div>
            <div class="widget-body">
                    <div class="row">
           <div class="col-sm-6">
            <div class="food-item white" v-for="(item) in cartItems" :key="item.id">
                <b-row>
                  <div class="col-xs-12 col-sm-6 col-lg-6 mb-3">
                    <div class="item-img pull-left">
                      <a class="restaurant-logo pull-left" v-if="item.thumb">
                        <b-img :src="item.thumb" :width="75" :height="75" :alt="item.name"/>
                      </a>
                      <a class="restaurant-logo pull-left" v-else>
                        <b-img src="../../../assets/img/checkout/imagemindisponivel.png" :width="75" :height="75" :alt="item.name"/>
                      </a>
                    </div>
                      <div class="rest-descr">
                        <h4>
                          <a>{{item.name}}</a>
                        </h4>
                        <h6>{{item.ingredients}}</h6>
                        <h6 v-if="item.quantity > 0">Quantidade: {{item.quantity}}</h6>
                        <h6 v-if="item.options.required.name">
                          <span style="font-size: 15px; font-weight: 700;">
                            <i class="fa fa-exclamation-circle pr-1"></i>{{item.options.required.name}} · R$ {{item.options.required.price}}
                          </span>
                          <hr>
                          <span style="font-size: 16px; font-weight: 300;" v-for="(j) in item.options.optional" :key="j.id">
                            <i class="fa fa-plus-circle pr-1"></i>{{j.name}} · R$ {{j.price}}
                          </span>                            
                        </h6>
                      </div>
                  </div>
                      <div class="col-xs-6 col-sm-6 col-lg-6" v-if="item.options.required.price">
                        <span class="price pull-right">R$ {{item | sumOptionals}}</span>
                      </div>
                      <div class="col-xs-6 col-sm-6 col-lg-6" v-else>
                        <span class="price pull-right">R$ {{item | sumOptionals}}</span>
                      </div>
                </b-row>
            </div>
                            
            </div>
              <div class="col-sm-6">
                <!-- ENDEREÇO DE ENTREGA -->
                <div v-if="delivery == 1 && cartFrete">
                <div class="cart-totals-title pb-1">
                    <h4><i class="fa fa-map pr-2"></i>ENDEREÇO DE ENTREGA</h4>
                  </div>
                  <span style="color: red; font-weight: 400; font-size: 0.9rem;"><i class="fa fa-exclamation-triangle pr-2"></i>Só finalize o pedido se o endereço estiver correto</span>
                    <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" v-if="userAddress.street">
                      {{userAddress.street}} • Nº {{userAddress.number}}<br/><div v-if="userAddress.complement">
                      Complemento: {{userAddress.complement}}<br/></div><div v-else-if="!userAddress.complement">
                      Complemento: -</div>
                      Bairro: {{userAddress.neighborhood}}<br/>
                      {{userAddress.city}} • {{userAddress.state}}<br/>
                  <em slot="footer">
                    <b-btn @click="goToAddress()" variant="primary" class="mr-2"><i class="fa fa-arrow-right pr-1"></i> ALTERAR</b-btn>
                  </em>
                    </b-card>
                    <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" v-else>
                     <h4 style="color: red; font-size: 1.3em; font-weight: normal;">VOCÊ NÃO SELECIONOU UM ENDEREÇO PARA ENTREGA! <i class="fa fa-frown-o"></i></h4>
                     <em slot="footer">
                       <b-btn @click="goToAddress()" size="sm" variant="outline-primary" class="mr-2"><i class="fa fa-eye pr-1"></i> VER MEUS ENDEREÇOS</b-btn>
                     </em>
                    </b-card>
                  </div>
                <!-- ./ENDEREÇO DE ENTREGA-->
                <!-- ENDEREÇO PARA RETIRADA -->
                <div v-else>
                    <div class="cart-totals-title pb-2">
                    <h4><i class="fa fa-building pr-2"></i>ENDEREÇO PARA RETIRADA</h4>
                  </div>
                    <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" v-if="restaurantAddress.street">
                      {{restaurantAddress.street}} • Nº {{restaurantAddress.number}}<br/><div v-if="restaurantAddress.complement">
                      Complemento: {{restaurantAddress.complement}}<br/></div>
                      Bairro: {{restaurantAddress.neighborhood}}<br/>
                      {{restaurantAddress.city}} • {{restaurantAddress.state}}<br/>
                    </b-card>
                    <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" v-else>
                     <h4 style="color: red; font-size: 1.3em; font-weight: normal;">VOCÊ NÃO SELECIONOU UM ENDEREÇO PARA ENTREGA! <i class="fa fa-frown-o"></i></h4>
                     <em slot="footer">
                       <b-btn @click="goToAddress()" size="sm" variant="outline-primary" class="mr-2"><i class="fa fa-eye pr-1"></i> VER MEUS ENDEREÇOS</b-btn>
                     </em>
                    </b-card>
                  </div>
                <!-- ./ENDEREÇO PARA RETIRADA -->
                  <!-- FORMA DE PAGAMENTO -->
                  <div class="cart-totals-title">
                         <h4><i class="fa fa-credit-card pr-2"></i>FORMA DE PAGAMENTO</h4>
                      </div>
                          <div class="payment-option">
                            <v-select v-model="selected" label="name" :options="options" class="mb-3" placeholder="Selecione uma opção">
                              <!-- dropdown pra selecionar -->
                              <template slot="option" slot-scope="option">
                                <div class="d-center">
                                  <b-img class="payment-img" rounded :src='option.img'/>
                                  {{ option.name }}
                                  </div>
                              </template>
                              <!-- método já selecionado -->
                              <template slot="selected-option" slot-scope="option">
                               <div class="selected d-center" v-if="selected.name == 'Dinheiro'">
                                <b-img rounded :src='option.img'/> 
                                {{ option.name }}
                                <span class="ml-2" v-if="change != 0">•
                                <vue-numeric read-only currency="R$" separator="." v-model="change" v-bind:precision="2" v-bind:max="1000"></vue-numeric>
                                </span>
                               </div>
                               <div class="selected d-center" v-else>
                                <b-img rounded :src='option.img'/>
                                {{ option.name }}
                               </div>
                              </template>
                            </v-select>
                          </div>
                          <!-- ALTERAR TROCO -->
                         <b-card header-title="troco" header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" v-if="selected.name == 'Dinheiro'" align="center">
                          <h4 style="color: green; font-size: 1.3rem; font-weight: 600;">Troco: <vue-numeric read-only currency="R$" separator="." v-model="change" v-bind:precision="2" v-bind:max="1000"></vue-numeric></h4>
                          <span style="color: red; font-size: 0.85rem; font-weight: normal;">Caso não precise de troco, deixe R$ 0,00</span>
                          <em slot="footer">
                            <b-btn size="sm" @click="modalShow = !modalShow" variant="outline-primary" class="mr-2"><i class="fa fa-edit pr-1"></i> ALTERAR TROCO</b-btn>
                          </em>
                    </b-card>
                    <!-- ./ALTERAR TROCO -->
                  <!-- ./FORMA DE PAGAMENTO -->
                  <!-- CUPOM 
                        <div class="cart-totals-title">
                         <h4><i class="fa fa-ticket pr-2"></i>CUPOM</h4>
                        </div>
                          <div class="payment-option">
                            <b-row class="mt-3 mb-3">
                              <b-col sm="6">
                                <b-form-input size="sm" type="text" placeholder="Código da promoção"></b-form-input>                      
                              </b-col>
                              <b-col sm="6">
                              <b-btn block variant="outline-success" size="sm">APLICAR DESCONTO</b-btn>
                              </b-col>
                            </b-row>
                          </div>
                    -->
                  <!-- CASHBACK 
                  <div v-if="restaurant.percentage_discount">
                        <div class="cart-totals-title">
                         <h4><i class="fa fa-money pr-2"></i>SALDO EM CRÉDITOS: R$200</h4>
                        </div>
                          <div class="payment-option">
                            <b-row class="mt-3 mb-3">
                              <b-col sm="6">
                                <b-form-checkbox id="cashbackCheckbox" value="accepted" unchecked-value="not_accepted">Cashback</b-form-checkbox>
                              </b-col>
                            </b-row>
                          </div>
                  </div>
                    -->
                            <div class="cart-totals margin-b-20">
                                <div class="cart-totals-title">
                                    <h4><i class="fa fa-calculator pr-2"></i>RESUMO DO PEDIDO</h4>
                                </div>
                                <div class="cart-totals-fields">
                                    <table class="table">
                                        <tbody>
                                            <tr v-if="cartFrete && userAddress.street">
                                                <td>SUBTOTAL</td>
                                                <td style="color: green;" v-if="cartSubTotal"><strong><vue-numeric read-only currency="R$" separator="." :value="cartSubTotal" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong></td>
                                            </tr>
                                            <tr v-if="discount.status == true">
                                                <td>DESCONTO: <i>{{discount.name}}</i></td>
                                          <!-- criar função no cart para fazer o cálculo do desconto -->
                                                <td style="color: red;"><strong>-10%</strong></td>
                                            </tr>
                                            <tr v-if="delivery == 1 && cartFrete">
                                                <td>FRETE</td>
                                                <td style="color: green;"><strong><vue-numeric read-only currency="R$" separator="." :value="cartFrete" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong></td>
                                            </tr>
                                            <tr v-if="delivery == 1">
                                                <td class="text-color">
                                                    <strong>TOTAL</strong>
                                                </td>
                                                <td class="text-color">
                                                    <strong><vue-numeric read-only currency="R$" separator="." :value="cartTotal" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                                                </td>
                                            </tr>
                                            <tr v-else>
                                                <td class="text-color">
                                                    <strong>TOTAL</strong>
                                                </td>
                                                <td class="text-color">
                                                    <strong><vue-numeric read-only currency="R$" separator="." :value="cartSubTotal" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            <b-btn @click="sendOrder(newCart)" class="button-order">FINALIZAR PEDIDO</b-btn>
                            </div>
                        </div>
                    </div>
            </div>
            <!-- Modal Troco -->
            <b-modal v-model="modalShow" title="Troco" title-tag="h4" cancel-title="Cancelar" header-bg-variant="warning" align="center" cancel-variant="danger" footer-bg-variant="light">
               <b-form>
                <b-form-group label="Insira um valor" description="<span style='color: red;'>Caso não precise de troco, deixe R$ 0,00</span>">
                    <vue-numeric currency="R$" separator="." v-model="change" v-bind:precision="2" v-bind:max="1000"></vue-numeric>
                </b-form-group>
              </b-form>
            </b-modal>
            <!-- ./Modal Troco -->
    </div>
    </b-container>
    <app-how-get-apps></app-how-get-apps>
  </div>
</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      selected: '',
      options: [],
      change: '', // troco (caso for dinheiro)
      modalShow: false,
      newCart: {}
    }
  },
  components: {
    AppHowGetApps
  },
  computed: {
    ...mapGetters({
      discount: 'getDiscount',
      restaurant: 'getRestaurant',
      restaurantAddress: 'getRestaurantAddress',
      cartItems: 'getCartItems',
      cartFrete: 'getFrete',
      cartSubTotal: 'cartSubTotal',
      cartTotal: 'cartTotal',
      delivery: 'getDelivery',
      userAddress: 'getUserAddress',
      cart: 'getCart'
    })
  },
  methods: {
    availablePayments () {
      let methods = this.restaurant.payment.map(function (i) {
        if (i.card != null) {
          return {
            'id': i.id,
            'name': i.name + ' ' + i.card,
            'card': i.card,
            'img': i.img,
            'is_online': i.is_online,
            'RestaurantsPayments': i.RestaurantsPayments
          }
        }
        return {
          'id': i.id,
          'name': i.name,
          'img': i.img,
          'card': i.card,
          'is_online': i.is_online,
          'RestaurantsPayments': i.RestaurantsPayments
        }
      })
      return methods
    },
    goToAddress () {
      this.$router.push('/enderecos')
    },
    async sendOrder (newCart) {
      if (!this.selected) {
        this.$swal({position: 'center', type: 'error', title: 'Selecione uma forma de pagamento!'})
      } else if (this.selected) {
        if (this.selected.name === 'Dinheiro' && this.change !== '') {
          this.$store.dispatch('setPaymentCash', this.change)
        }
        this.$store.dispatch('setTotal', this.cartTotal)
        this.$store.dispatch('setPayment', this.selected)
        newCart = this.cart
        let response = []
        try {
          let _axios = this.$http.create()
          _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
          response = await _axios.post(
            'orders', newCart)
        } catch (e) {
          console.log(e)
        }
        this.$swal({position: 'center', type: 'success', title: 'Finalizar Pedido', text: 'O pedido n. ' + response.data.data.id + ' foi realizado com sucesso! 👍', showConfirmButton: false, timer: 2500})
        this.$store.dispatch('emptyCart')
        this.$router.push('/pedidos')
      }
    }
  },
  filters: {
    Upper (value) {
      return value.toUpperCase()
    },
    sumOptionals (item) {
      let total = 0
      if (item.options.required.price && !item.options.optional.length) {
        total += item.options.required.price * item.quantity
      } else if (item.options.required.price && item.options.optional.length) {
        let optionalsArrayPrice = item.options.optional.map(function (i) {
          return i.price
        })
        let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
        total += (item.options.required.price + optionalsPrice) * item.quantity
      } else {
        total += item.price * item.quantity
      }
      return total
    }
  },
  created () {
    // popula options
    this.options = this.availablePayments()
  }
}
</script>

<style scoped>
.cart-addon .food-item {
    margin-bottom: 20px;
    border-bottom: 1px solid #ededed;
    padding: 2px 0 15px;
}

.cart-addon .food-item:last-of-type {
    border-bottom: none;
}

.cart-addon .food-item .item-img b-img {
    max-width: 70px;
}

.cart-addon .food-item .rest-descr {
    margin-left: 80px;
}

.cart-addon .food-item h6 span {
    font-size: 16px;
    color: #4d4f56;
    display: block;
    margin: 4px 0 0 0;
    line-height: 20px;
}

.cart-addon .food-item .price {
    font-weight: 600;
    margin-top: 15px;
    display: block;
    color: #25282b;
}

.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.widget-heading .widget-title {
    font-size: 16px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.widget-body {
    padding: 20px;
}

.custom-control {
    display: inline-block;
}

.payment-img {
  height: auto;
  max-width: 2.5rem;
  margin-right: 1rem;
}

.d-center {
  display: flex;
  align-items: center;
}

.selected img {
  width: auto;
  max-height: 23px;
  margin-right: 0.5rem;
}

.v-select .dropdown li {
  border-bottom: 1px solid rgba(112, 128, 144, 0.1);
}

.v-select .dropdown li:last-child {
  border-bottom: none;
}

.v-select .dropdown li a {
  padding: 10px 20px;
  width: 100%;
  font-size: 1.25em;
  color: #3c3c3c;
}

.v-select .dropdown-menu .active > a {
  color: #fff;
}

.button-order {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
	background-color: #3CB371;
}
</style>



