<template>
<!-- Featured restaurants starts -->
<section class="featured-restaurants" v-if="lastUsed.city == 'Uberlândia'">
    <b-container>
        <b-row>
            <b-col sm="4">
                <div class="title-block pull-left">
                    <h4>Em Destaque 🔥</h4>
                </div>
            </b-col>
        </b-row>
        <b-row class="mt-4 mb-5">
            <b-col cols="12" sm="6" class="mb-4" v-for="(restaurant) in featuredRestaurants" :key="restaurant.id">
                <div class="restaurant-wrap">
                    <b-row>
                        <b-col cols="12" sm="3" class="text-center">
                            <b-link class="restaurant-logo" :to="{name: 'profile', params: { id: restaurant.id }}">
                                <b-img-lazy blank-color="#bbb" rounded :src="restaurant.img" alt="Restaurant logo" />
                            </b-link>
                        </b-col>
                        <b-col cols="12" sm>
                            <b-row>
                                <b-col>
                                    <h5>
                                        <b-link class="" :to="{name: 'profile', params: { id: restaurant.id }}">{{restaurant.name}}</b-link>
                                    </h5>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col>
                                    <span>{{restaurant.Categories | category}}</span>
                                </b-col>
                            </b-row>
                            <b-row class="mt-2" style="font-size: 12px;">
                                <b-col cols="auto">
                                    <span>
                                        <i class="fa fa-motorcycle"></i> {{restaurant.delivery_min}}</span>
                                </b-col>
                                <b-col cols="auto">
                                    <div class="ratings">
                                        <span v-if="restaurant.stars != null">
                                             <star-rating v-model="restaurant.stars" :inline="true" :star-size="15" :read-only="true" :increment="0.01" :fixed-points="2" :show-rating="false"></star-rating>
                                            <!--
                                            <i v-for="(n) in Math.round(restaurant.stars)" :key="n" class="fa fa-star"></i>
                                            <span v-if="Math.round(restaurant.stars) < 5">
                                                <i v-for="(i) in (5 - Math.round(restaurant.stars))" :key="i" class="fa fa-star-o"></i>
                                            </span>-->
                                        </span>
                                        <span v-else-if="restaurant.stars == null">
                                            <p class="new"><strong>NOVO!</strong> <i class="fa fa-fire ml-1"></i></p>
                                        </span>
                                    </div>
                                </b-col>
                            </b-row>
                        </b-col>
                    </b-row>
                </div>
            </b-col>
        </b-row>
        <!-- add restaurant starts -->
        <section class="add-restaurants">
            <div class="container">
                <div class="row">
                    <div class="col-xs-12 col-sm-3 add-title">
                        <h4>É um estabelecimento?</h4>
                    </div>
                    <div class="col-xs-12 col-sm-5 join-text">
                        <p>Junte-se aos outros restaurantes que se beneficiam de ter seus cardápios no
                            <router-link to="/restaurantes/">
                            <a>
                                <strong>diretório do Sacia Fome</strong>
                            </a>
                            </router-link>
                        </p>
                    </div>
                    <div class="col-xs-12 col-sm-4 join-btn text-sm-right">
                        <b-btn size="lg" variant="theme" to="/novo-restaurante"><i class="fa fa-cutlery mr-1"></i> Quero ser parceiro</b-btn>
                    </div>
                </div>
            </div>
        </section>
        <!-- add restaurant ends -->
    </b-container>
</section>
<!-- Featured restaurants ends -->
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      featuredRestaurants: []
    }
  },
  computed: {
    ...mapGetters({
      currentPosition: 'currentPosition',
      lastUsed: 'lastUsed'
    })
  },
  methods: {
    async getFeaturedRestaurants () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng + '/offset/1/limit/4'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  created () {
    this.getFeaturedRestaurants().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.featuredRestaurants = response1
    })
  },
  filters: {
    category (categories) {
      return categories.map(category => category.name).join(' · ')
    }
  }
}
</script>

<style scoped>
/****** featured restaurants block *******/

.new {
    color: #ffb42d;
}

.featured-restaurants {
  padding: 100px 0 20px !important;
  position: relative !important;
}

@media (min-width: 280px) and (max-width: 580px) {
  .featured-restaurants {
    padding: 50px 0 20px !important;
    position: relative !important;
  }
}

.featured-restaurants:before {
  position: absolute;
  top: -81px;
  left: 50px;
  background: url(../../../assets/img/home/chilly.png);
  width: 103px;
  height: 151px;
  content: "";
}

@media (min-width: 280px) and (max-width: 580px) {
  .featured-restaurants:before {
    display: none;
  }
}

.featured-restaurants >>> .restaurant-wrap {
    border: 1px solid #eaebeb;
    padding: 15px;
    border-radius: 3px;
    background: #fafaf8;
}

.featured-restaurants .restaurant-logo {
    border-radius: 3px;
}

.featured-restaurants .restaurant-logo img {
    width: 95px;
    height: auto;
    border: 1px solid #eaebeb;
}

@media (min-width: 320px) and (max-width: 543px) {
    .featured-restaurants .restaurant-logo img {
        width: 85px;
        margin-bottom: 15px;
    }
}

.featured-restaurants .restaurant-wrap h5 {
    margin-top: 5px;
    font-weight: 600;
}

.featured-restaurants .restaurant-wrap h5 a {
    color: #25282b;
}

.featured-restaurants .ratings span i {
    color: #ffb42d;
}

.add-restaurants {
  background: #fafaf8;
  border: 1px solid #eaebeb;
  padding: 35px 10px 23px;
  border-radius: 2px;
}

.add-restaurants p strong {
  font-weight: 600;
  color: #ffb42d;
}

@media (min-width: 320px) and (max-width: 543px) {
  .add-restaurants {
    text-align: center;
  }
  .add-restaurants a.btn.btn-theme {
    display: block;
  }
}
</style>



