<template>
    <div class="widget clearfix">
        <!-- /widget heading -->
        <div class="widget-heading">
            <h3 class="widget-title text-dark">
                Faixa de preço
                <i class="fa fa-money pull-right"></i>
            </h3>
            <div class="clearfix"></div>
        </div>
        <div class="widget-body">
            <div class="range-slider m-b-10">
                <span id="ex2CurrentSliderValLabel"> Filtre pelo valor:
                    <span id="ex2SliderVal">
                        <strong>R$ {{ basicValue }}</strong>
                    </span>
                </span>
                <br>
                <b-form-slider id="ex2" v-model="basicValue" :min="2" :max="100" @change="change" trigger-change-event></b-form-slider>
            </div>
        </div>
    </div>
    <!-- end:Pricing widget -->
</template>

<script>
import {bFormSlider} from 'vue-bootstrap-slider'
import 'bootstrap-slider/dist/css/bootstrap-slider.css'

export default {
  data: function () {
    return {
      basicValue: 35
    }
  },
  methods: {
    change: function (value) {}
  },
  components: {
    bFormSlider
  }
}
</script>

<style scoped>
.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.menu-widget .widget-heading {
    border-bottom: none
}

.widget-heading .widget-title {
    font-size: 16px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 2px;
    position: relative;
}

.widget-body {
    padding: 20px;
}

/* #ex2 >>> .slider-track-low {
	background: #ffb42d !important;
} */

#ex2 >>> .slider-selection {
	background: #ffb42d !important;
}

#ex2 >>> .slider-handle {
    background: #ffffff !important;
    filter: none !important;
    box-shadow: 0 0 3px 3px rgba(93, 92, 99, 0.05) !important;
    border: 1px solid #ededed !important;
}

#ex2SliderVal {
    font-weight: 600;
    padding: 0 5px;
    color: #3a3e47;
}
</style>



