<template>
<transition name="fade">
    <div class="registration page-wrapper">
        <!-- Using button-content slot -->
        <template slot="button-content">
        <b>Entrar ou cadastrar</b>
        </template>
        <!-- <b-dropdown-item> -->
        <div class="login-dropdown-content">
            <b-row>
                <div class="col-md-4">
                  
                </div>
                <div class="col-md-4">
                    <h4 align="center" class="title">Login do Cliente</h4>
                    <b-form>
                        <div class="form-group">      
                            <b-form-input class="form-control input-form mr-sm-2" type="email" placeholder="seuemail@exemplo.com" required v-model="user.email"></b-form-input>                   
                        </div>
                        <div class="form-group">
                            <b-form-input class="form-control input-form mr-sm-2" type="password" placeholder="Senha" required v-model="user.password"></b-form-input>
                        </div>

                        <div class="form-group">
                            <b-button class="btn btn-theme btn-block button-login" @click="login()"><i class="fa fa-sign-in mr-2"></i>Entrar</b-button>
                        </div>    
                    </b-form>
                    <p align="center">ou</p>
                    <div class="social-buttons">
                        <fb-signin-button class="button-facebook"
                          :params="fbSignInParams"
                          @success="onSignInSuccess"
                          @error="onSignInError">
                          <i class="fa fa-facebook" aria-hidden="true"></i>
                          Acessar com Facebook
                        </fb-signin-button>
                    </div>
                    <div class="bottom text-center">
                      Novo aqui ?
                      <b-link to="cadastro"><b>Junte-se a nós!</b></b-link>
                    </div>
                    <div class="bottom text-center">
                        Esqueceu a senha ?
                        <b-link to="esqueci-senha"><b>Recuperar</b></b-link>
                    </div>
                </div>
            </b-row>
        </div>
    </div>
</transition>
</template>

<script>
export default {
  data () {
    return {
      user: {
        email: '',
        password: ''
      },
      fbSignInParams: window.fbSignInParams
    }
  },
  methods: {
    login () {
      this.$store.dispatch('login', {
        email: this.user.email,
        password: this.user.password
      }).then((data) => {
        if (data.status) {
          this.$swal({position: 'center', showConfirmButton: false, timer: 3000, type: 'success', title: 'Bem Vindo(a)!', text: 'Escolha um estabelecimento e faça seu pedido! 😋'})
          this.$router.push('/restaurantes')
        } else {
          this.$toasted.error(data.message).goAway(3000)
        }
      })
    },
    socialLogin (payload) {
      this.$store.dispatch('socialLogin', payload).then((data) => {
        this.$router.push('/restaurantes')
      })
    },
    onSignInSuccess (response) {
      window.FB.api('/me?fields=name,email,gender', user => {
        user.picture = `https://graph.facebook.com/${response.authResponse.userID}/picture?type=large`
        let json = {
          img: user.picture,
          first_name: user.name,
          last_name: '',
          email: user.email,
          gender: user.gender,
          authenticate: 'facebook',
          token: response.authResponse.accessToken
        }
        this.socialLogin(json)
      })
    },
    onSignInError (error) {
      console.log('OH NOES', error)
    },
    onLogout () {
      this.isConnected = false
    },
    onLogin () {
      this.facebookLogin()
    }
  }
}
</script>

<style scoped>
.navbar-light .navbar-nav .nav-link {
    color: black;
}

.login-dropdown-content {
    min-width: 250px;
    padding: 14px 14px 0;
    overflow: hidden;
}

.login-dropdown-content .bottom {
    border-top: 1px solid #ddd;
    clear: both;
    padding: 14px;
    width: 100%;
}

.login-dropdown-content .social-buttons {
    margin: 12px 0;
}

.login-dropdown-content .social-buttons button {
    width: 100%;
}

.login-dropdown-content .form-group {
    margin-bottom: 10px;
}

.title {
  color: #4f4f4f;
  text-align: center;
  font-weight: 600;
  font-size: 25px;
  padding-top: 20px;
}

.button-facebook {
    color: #fff;
    background-image: linear-gradient(#4C69BA, #3B55A0);
    border: 0 !important;
    color: white;
    height: 50px;
    line-height: 20px;
    border-radius: 8px;
    box-sizing: border-box;
    display: block;
    font-size: 1.6em;
    font-weight: normal;
    padding-bottom: 12px;
    padding-top: 12px;
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.btn-fb:hover {
    color: #fff;    
    background-image: linear-gradient(#4C69BA, #3B55A0);
}

@media(max-width:768px) {
    .login-dropdown-content {
        background-color: inherit;
        color: black;
    }
    .login-dropdown-content .bottom {
        background-color: inherit;
        border-top: 0 none;
    }
}

.input-form {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-login {
  border: 0 !important;
  color: white;
  height: 50px;
  line-height: 20px;
  border-radius: 8px;
  box-sizing: border-box;
  display: block;
  font-size: 1.6em;
  font-weight: normal;
  padding-bottom: 12px;
  padding-top: 12px;
  width: 100%;
  background-color: #ffb42d;
}

</style>



