<template>
    <div class="home">
        <app-banner></app-banner>
        <app-location-match></app-location-match>
        <app-popular></app-popular>
        <app-how-it-works></app-how-it-works>
        <app-featured-restaurants></app-featured-restaurants>
        <app-how-get-apps></app-how-get-apps>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppBanner from './partials/home/Banner'
import AppFeaturedRestaurants from './partials/home/FeaturedRestaurants'
import AppHowItWorks from './partials/home/HowItWorks'
import AppLocationMatch from './partials/home/LocationMatch'
import AppPopular from './partials/home/Popular'
import AppHowGetApps from '@/components/root/HowGetApps'
export default {
  components: {
    AppBanner,
    AppFeaturedRestaurants,
    AppHowItWorks,
    AppLocationMatch,
    AppPopular,
    AppHowGetApps
  },
  data () {
    return {}
  },
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn'
    })
  },
  created () {
    if (this.isLoggedIn) {
      this.$router.push({ name: 'restaurants' })
    }
  }
}
</script>

<style scoped>

</style>



