<template>
    <section class="contact-page inner-page">
        <div class="container">
            <div class="row">
                <!-- REGISTER -->
                <div class="col-md-8">
                    <div>
                        <div class="widget-body">
                            <div class="row">
                                <div class="col-sm-3"></div>
                                <div class="col-sm-9">
                                    <span class="title">QUERO ME CADASTRAR!</span>
                                </div>
                            </div>
                            <form>
                                <div class="row">
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Nome</label>
                                        <input class="form-control input-form" type="text" id="example-text-input" v-model="user.first_name">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Sobrenome</label>
                                        <input class="form-control input-form" type="text" id="example-text-input-2" v-model="user.last_name">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Email</label>
                                        <input type="email" class="form-control input-form" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Email" v-model="user.email">
                                        <small id="emailHelp" class="form-text text-muted">Nós nunca compartilharemos seu email com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Telefone</label>

                                        <the-mask class="form-control input-form" v-model="user.phone" masked="true" :mask="['(##) ####-####', '(##) #####-####']" />
                                        <small class="form-text text-muted">Nós nunca compartilharemos seu telefone com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword1" v-model="user.password">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Confirmar Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword2" v-model="user.repeatPassword">
                                    </div>
                                </div>
                                <div class="row">
                                    <b-button class="button-register" @click="register()" >Cadastrar</b-button>
                                </div>
                            </form>
                        </div>
                        <!-- end: Widget -->
                    </div>
                    <!-- /REGISTER -->
                </div>
                <!-- WHY? -->
                <div class="col-md-4">
                    <h4>O registro é rápido, fácil e gratuito.</h4>
                    <hr>
                    <img src="../../../assets/img/home/registration.jpeg" alt="Depois de registrado" class="img-fluid">
                    <p></p>
                    <div role="tablist">
                        <b-card no-body class="mb-1">
                            <b-card-header header-tag="header" class="p-0" role="tab">
                                <!--<b-link v-b-toggle.accordion1>{{ question }}</b-link>-->
                                <b-btn block v-b-toggle.accordion1 variant="theme">Preciso ser registrado para dar uma olhada nos <br>restaurantes e cardápios?</b-btn>
                            </b-card-header>
                            <b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <p class="card-text">
                                        {{ answer }}
                                    </p>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                    </div>
                    <!-- end:Panel -->
                    <h4 class="m-t-20">Entre em contato com o suporte ao cliente.</h4>
                    <p> Se você está procurando por mais ajuda ou tenha uma pergunta a fazer, por favor </p>
                    <p>
                      <b-btn style="border-radius: 8px; font-weight: normal; background-color: #ffb42d; color: white;" block to="fale-conosco" class="mb-2">
                        <i class="fa fa-envelope mr-1"></i> Contate-nos</b-btn>
                        <!--<b-link to="fale-conosco" class="btn btn-theme m-t-15">Contate-nos</b-link>-->
                    </p>
                </div>
                <!-- /WHY? -->
            </div>
        </div>
    </section>
</template>

<script>
export default {
  data () {
    return {
      answer: 'Não precisa. Navegue nos mais variados restaurantes e pratos a sua disposição!',
      user: {},
      confirmPassword: ''
    }
  },
  methods: {
    register () {
      this.$http.post('/users', this.user).then((response) => {
        response = response.data
        if (response.status) {
          this.$store.dispatch('login', this.user).then(() => {
            console.log('Response')
            this.$router.push('/restaurantes')
          })
        } else {
          this.$toasted.error(response.message).goAway(3000)
        }
      })
    }
  }
}
</script>

<style scoped>
.widget {
    border: 0px solid #eaebeb;
    background: white;
    border-radius: 3px;
    margin-bottom: 20px;
    position: relative;
}

.widget-body {
    padding: 20px;
}

.title {
    color: #4f4f4f;
    text-align: center;
    font-weight: 600;
    font-size: 25px;
    padding-bottom: 20px;
    margin-left: 35px;
}

.input-form {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-register {
    border: 0 !important;
    color: white;
    height: 50px;
    line-height: 20px;
    border-radius: 8px;
    box-sizing: border-box;
    display: block;
    font-size: 1.6em;
    font-weight: normal;
    padding-bottom: 12px;
    padding-top: 12px;
    width: 100%;
    background-color: #ffb42d;
    margin-left: 12px;
    margin-right: 12px;
}
</style>



