<template>
  <headroom :classes="classes" :zIndex="1050">
    <header id="header">
      <b-navbar toggleable="md" type="light" variant="faded">
        <div class="container">

          <b-navbar-toggle target="nav_collapse">
          </b-navbar-toggle>

          <b-navbar-brand to="/">
            <b-img src="../../../assets/logo-saciafome.png" alt="SaciaFome Logo" />
          </b-navbar-brand>

          <b-collapse is-nav id="nav_collapse">
            <b-navbar-nav v-if="lastUsed.city" fill>
              <b-nav-item v-if="!isLoggedIn && lastUsed.city">
               <span class="location">
                <i class="fa fa-map-marker mr-2"></i>
                {{lastUsed.city}}
                </span>
              </b-nav-item>
              <b-nav-item v-if="isLoggedIn && userAddress.street">
                <span class="location" @click="restaurantes">
                 <i class="fa fa-map-marker mr-2"></i>
                 {{lastUsed.street}} • Nº {{lastUsed.number}}
                 </span>
                 <span class="change-address ml-2" @click="alterar">Alterar Endereço</span>
              </b-nav-item>
              <b-nav-item v-if="isLoggedIn && !userAddress.street" @click="alterar">
                <span class="location">
                <i class="fa fa-map-marker mr-2"></i>
                {{lastUsed.city}}
                </span>
                <span class="change-address ml-2" @click="alterar">Meus Endereços</span>
            </b-nav-item>
            </b-navbar-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="mb-2 ml-auto">
              <app-logged-dropdown v-if="isLoggedIn"></app-logged-dropdown>
              <app-login-dropdown v-else></app-login-dropdown>
            </b-navbar-nav>

          </b-collapse>
        </div>
      </b-navbar>
    </header>
  </headroom>
<!-- navbar.vue -->
</template>

<script>
import { mapGetters } from 'vuex'
import { headroom } from 'vue-headroom'
import AppLoggedDropdown from './login/LoggedDropdown'
import AppLoginDropdown from './login/LoginDropdown'

export default {
  data () {
    return {
      classes: {
        initial: '',
        pinned: 'fade show',
        unpinned: 'fade'
      },
      menuClose: false
    }
  },
  components: {
    headroom,
    AppLoggedDropdown,
    AppLoginDropdown
  },
  methods: {
    alterar () {
      this.$router.push('/enderecos')
    },
    restaurantes () {
      this.$router.push('/restaurantes')
    }
  },
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn',
      lastUsed: 'lastUsed',
      userAddress: 'getUserAddress'
    })
  }
}
</script>

<style scoped>
#header {
    position: sticky;
    width: 100%;
    border: 0;
    z-index: 99;
}

.headroom--pinned {
    display: block;
}

.headroom--unpinned {
    display: none;
}

.navbar {
    background-image: none;
    background-color: rgba(255, 180, 45, 0.8);
    padding: 0rem 1rem;
    border-radius: 0;
}

.container {
    max-width: 1900px;
    width: 100%;
}

.nav-link-custom {
  color: black !important;
}

.location {
  font-size: 20px;
  color: #111111;
}

.location-null {
  font-size: 20px;
  color: #2F4F4F;
}

.change-address {
  font-size: 0.76em;
  font-weight: normal;
  color: #111111;
  text-decoration: underline;
}

</style>



