<template>
    <!-- top Links -->
    <div class="top-links">
        <b-container>
            <b-nav fill class="justify-content-center links">
                <b-nav-item :class="['link-item', { active: item.isActive}]" :to="item.path" v-for="item in items" :key="item.id">
                    <span>{{item.id}}</span> {{item.description}}
                </b-nav-item>
            </b-nav>
        </b-container>
    </div>
    <!-- end:Top links -->
</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    items () {
      let path = this.$route.path.substr(1)
      let aux = [
        { id: 1, description: 'Escolha a sua localização', path: {name: 'address'}, isActive: true },
        { id: 2, description: 'Escolha o restaurante', path: {name: 'restaurants'}, isActive: false },
        { id: 3, description: 'Escolha a sua comida favorita', path: {name: 'restaurants', params: { id: 1 }}, isActive: false },
        { id: 4, description: 'Encomendar e pagar', path: {name: 'checkout'}, isActive: false }
      ]
      return aux.map((value, index) => {
        let isActive = value.path === path
        if (isActive) {
          value.path = ''
          value.isActive = isActive
        }
        return value
      })
    }
  },
  methods: {
    findActive () {
      let array = this.items
      let id1 = array.find(function (obj) { return obj.id === 1 })
      let id2 = array.find(function (obj) { return obj.id === 2 })
      let id3 = array.find(function (obj) { return obj.id === 3 })
      let id4 = array.find(function (obj) { return obj.id === 4 })
      if (this.$route.path === '/restaurantes') {
        id1.isActive = true
        id2.isActive = true
      } else if (this.$route.path > '/restaurantes') {
        id1.isActive = true
        id2.isActive = true
        id3.isActive = true
      } else if (this.$route.path === '/pagamento') {
        id1.isActive = true
        id2.isActive = true
        id3.isActive = true
        id4.isActive = true
      }
    }
  },
  created () {
    this.findActive()
  }
}
</script>

<style scoped>
/********* top links part ***********/

.top-links {
  padding: 15px 0;
  border-bottom: 1px solid #eaebeb;
}

.top-links ul {
  margin-bottom: 0;
}

.link-item a {
  font-size: 12px;
  color: #414551;
}

.link-item.active span {
  background-color: #ffb42d;
  color: #fff;
}

.links span {
  border: 1px solid #eaebeb;
  border-radius: 100px;
  width: 32px;
  height: 32px;
  color: #3a3e47;
  font-weight: 600;
  display: inline-block;
  margin-right: 18px;
  text-align: center;
  line-height: 28px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.links li:hover span {
  background-color: #ffb42d;
  color: #fff;
}

@media (max-width: 800px) {
  .top-links {
    display: none;
  }
}
</style>


