<template>
  <div class="restaurants page-wrapper">
    <app-top-links id="v-step-1"></app-top-links>
    <div class="inner-page-hero bg-image" id="v-step-0"></div>
    <!--<app-inner-page-hero id="v-step-0"></app-inner-page-hero>-->
    <app-result-show :total="total" id="v-step-4"></app-result-show>
    <section class="restaurants-page">
      <b-container>
          <b-row>
            <b-col xs="12" sm="5" md="5" lg="3">
              <app-widget-sidebar-filter id="v-step-3" @update="onChildUpdate" :selected="selected" :categories="categories"></app-widget-sidebar-filter>
              <!--<app-widget-price-range></app-widget-price-range>-->
              <!-- <app-widget-popular-tags></app-widget-popular-tags> -->
            </b-col>
            <b-col xs="12" sm="7" md="7" lg="9">
              <vue-data-loading :loading="loading" :completed="completed" :listens="['infinite-scroll', 'pull-down']" :init-scroll="true" @infinite-scroll="infiniteScroll" @pull-down="pullDown">
                  <ul id="infinite-list">
                    <transition-group name="list" tag="p">
                    <app-restaurants-list v-for="(restaurant) in featuredRestaurants"
                    :restaurant="restaurant" :key="restaurant.id" id="v-step-2"></app-restaurants-list>
                    </transition-group>
                  </ul>
                  <div slot="infinite-scroll-loading">CARREGANDO...</div>
                  <div slot="completed">EM BREVE ESTAREMOS COM MAIS ESTABELECIMENTOS!</div>
              </vue-data-loading>
            </b-col>
          </b-row>
      </b-container>
    </section>
    <app-how-get-apps></app-how-get-apps>
    <v-tour name="myTour" :steps="steps">
  <template slot-scope="tour">
    <transition name="transition">
      <v-step
        v-if="tour.currentStep === index"
        v-for="(step, index) of tour.steps"
        :key="index"
        :step="step"
        :previous-step="tour.previousStep"
        :next-step="tour.nextStep"
        :stop="tour.stop"
        :isFirst="tour.isFirst"
        :isLast="tour.isLast"
      >
        <template v-if="tour.currentStep === 0">
          <div slot="actions">
            <button @click="tour.stop" class="v-step__button">Pular tutorial</button>
            <button @click="tour.nextStep" class="v-step__button">Próximo</button>
          </div>
        </template>
        <template v-if="tour.currentStep === 1">
          <div slot="actions">
            <button @click="tour.stop" class="v-step__button">Pular tutorial</button>
            <button @click="tour.previousStep" class="v-step__button">Anterior</button>
            <button @click="tour.nextStep" class="v-step__button">Próximo</button>
          </div>
        </template>
        <template v-if="tour.currentStep === 2">
          <div slot="actions">
            <button @click="tour.stop" class="v-step__button">Pular tutorial</button>
            <button @click="tour.previousStep" class="v-step__button">Anterior</button>
            <button @click="tour.nextStep" class="v-step__button">Próximo</button>
          </div>
        </template>
        <template v-if="tour.currentStep === 3">
          <div slot="actions">
            <button @click="tour.stop" class="v-step__button">Pular tutorial</button>
            <button @click="tour.previousStep" class="v-step__button">Anterior</button>
            <button @click="tour.nextStep" class="v-step__button">Próximo</button>
          </div>
        </template>
        <template v-if="tour.currentStep === 4">
          <div slot="actions">
            <button @click="tour.previousStep" class="v-step__button">Anterior</button>
            <button @click="tour.stop" class="v-step__button">Entendi!</button>
          </div>
        </template>
      </v-step>
    </transition>
  </template>
</v-tour>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppHowGetApps from '../components/root/HowGetApps'
import AppTopLinks from './partials/TopLinks'
import AppInnerPageHero from './partials/restaurants/InnerPageHero'
import AppResultShow from './partials/restaurants/ResultShow'
import AppWidgetSidebarFilter from './partials/restaurants/WidgetSidebarFilter'
import AppWidgetPriceRange from './partials/restaurants/WidgetPriceRange'
import AppWidgetPopularTags from './partials/WidgetPopularTags'
import AppRestaurantsList from './partials/restaurants/RestaurantsList'
import VueDataLoading from 'vue-data-loading'

export default {
  components: {
    AppTopLinks,
    AppInnerPageHero,
    AppResultShow,
    AppWidgetSidebarFilter,
    AppWidgetPriceRange,
    AppWidgetPopularTags,
    AppRestaurantsList,
    AppHowGetApps,
    VueDataLoading
  },
  data () {
    return {
      steps: [
        {
          target: '#v-step-0',
          content: `Bem vindo ao <strong>Sacia Fome</strong>!`,
          params: {
            placement: 'bottom'
          }
        },
        {
          target: '#v-step-1',
          content: 'Para começar, selecione um endereço no passo 1.'
        },
        {
          target: '#v-step-2',
          content: 'Os estabelecimentos mais próximos a sua localização serão listados abaixo.',
          params: {
            placement: 'top'
          }
        },
        {
          target: '#v-step-3',
          content: 'Filtre pela sua categoria preferida.',
          params: {
            placement: 'top'
          }
        },
        {
          target: '#v-step-4',
          content: 'Pronto!<br>Agora é só pedir sua comida favorita :)',
          params: {
            placement: 'top'
          }
        }
      ],
      featuredRestaurants: [],
      auxCategories: [],
      categories: [],
      selected: [],
      total: 0,
      nextItem: 1,
      loading: false,
      completed: false,
      page: 0
    }
  },
  computed: {
    ...mapGetters({
      currentPosition: 'currentPosition',
      newUser: 'newUser',
      isLoggedIn: 'isLoggedIn'
    })
  },
  methods: {
    fetchData () {
      if (this.selected && this.selected.length) {
        this.populateRestaurantsByCategories()
      } else {
        this.populateRestaurants()
      }
    },
    pullUp () {
      this.fetchData()
      this.page++
      console.log('pullUp: ' + this.page)
    },
    pullDown () {
      this.list = []
      this.page = 1
      this.completed = false
      this.fetchData()
      console.log('pull-down: ' + this.page)
    },
    infiniteScroll () {
      if (!this.loading) {
        this.fetchData()
        this.page++
        console.log('infiniteScroll: ' + this.page)
      }
    },
    async getFeaturedRestaurants () {
      let response = []
      try {
        this.loading = true
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          `/restaurant/latitude/${this.currentPosition.lat}/longitude/${this.currentPosition.lng}/offset/${this.page}/limit/20`
        )
        this.loading = false
      } catch (e) {
        console.log(e)
        this.loading = false
      }
      return response
    },
    async getFeaturedRestaurantsByCategories () {
      console.log('BUscando restaurantes por categorias: ' + this.page)
      let response = []
      try {
        this.loading = true
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          `/restaurant/category/${this.selected}/latitude/${this.currentPosition.lat}/longitude/${this.currentPosition.lng}/offset/${this.page}/limit/20`
        )
        this.loading = false
      } catch (e) {
        console.log(e)
        this.loading = false
      }
      return response
    },
    async getCategories () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/categories'
        )
      } catch (e) {
        console.log(e)
      }
      this.auxCategories = response.data.data
      this.categories = response.data.data.map(item => [item.name])
    },
    populateRestaurants () {
      this.getFeaturedRestaurants().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          if (response.data.length === 0) {
            this.completed = true
          }
          for (var i = 0; i < response.data.length; i++) {
            this.featuredRestaurants.push(response.data[i])
          }
          this.total = this.featuredRestaurants.length
        }
        return []
      })
    },
    populateRestaurantsByCategories () {
      this.getFeaturedRestaurantsByCategories().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          if (response.data.length === 0) {
            this.completed = true
          }
          for (var i = 0; i < response.data.length; i++) {
            this.featuredRestaurants.push(response.data[i])
          }
          this.total = this.featuredRestaurants.length
        }
        return []
      })
    },
    onChildUpdate (newValue) {
      this.selected = newValue
      this.featuredRestaurants = []
      this.page = 0
      if (this.selected && this.selected.length) {
        this.populateRestaurantsByCategories()
      } else {
        this.populateRestaurants()
      }
      this.page = 1
    }
  },
  created () {
    this.getCategories()
    this.populateRestaurants()
    this.page++
  },
  mounted () {
    if (this.newUser && this.isLoggedIn) {
      this.$tours['myTour'].start()
    } else if (!this.newUser && this.isLoggedIn) {
      this.$tours['myTour'].stop()
    } else if (!this.isLoggedIn) {
      this.$tours['myTour'].start()
    }
  }
}
</script>

<style scoped>

.inner-page-hero {
    padding-top: 7%;
    padding-bottom: 7%;
}

.bg-image {
    background: url('../assets/img/restaurants/banner-min.jpg') no-repeat center center;
    background-size: cover;
}

.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active em versões anteriores a 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}

.transition-enter-active,
.transition-leave-active {
  transition: all 1s ease-in-out;
}
.transition-enter,
.transition-leave-to {
  opacity: 0;
  transform: translate3d(600px, 0, 0);
}

</style>



