<template>
    <!-- start: Inner page hero -->
    <section class="inner-page-hero bg-image">
        <div class="profile">
            <b-container>
                <b-row>
                  <b-col cols="auto" sm="auto" xl="auto" md="auto" class="profile-img">
                        <div class="image-wrap">
                            <b-badge variant="transparent" class="open-ribbon" v-if="restaurant.open == 1"><span>ABERTO</span></b-badge>
                            <b-badge variant="transparent" class="closed-ribbon" v-else-if="restaurant.open == 0"><span>FECHADO</span></b-badge>
                            <figure v-if="restaurant.img">       
                                <img :src="restaurant.img" :alt="restaurant.name">
                            </figure>
                        </div>
                    </b-col>
                    <b-col cols="12" md="auto" class="profile-desc">
                        <div class="pull-left right-text white-txt">
                            <h6>{{restaurant.name}}</h6>
                            <p v-if="restaurant.Categories">{{restaurant.Categories | category}}</p>
                            <p v-if="restaurant.percentage_discount"><i class="fa fa-money pr-1" style="color: #ffd953;"></i> CASHBACK: <span class="badge-cash-back mx-2">{{restaurant.percentage_discount}} % </span></p>
                            <p v-if="frete == true"><span class="badge-free-shipping">Frete: Grátis</span></p>
                            <p v-if="restaurant.delivery_pickup == 1"><span style="margin-right: 5px;" class="badge-not-delivery"> Não entregamos </span></p>
                            <p v-if="restaurant.delivery_pickup == 0"><span style="margin-right: 5px;" class="badge-only-delivery"> Somente entregas </span></p>
                            <ul class="nav nav-inline">
                                <li class="nav-item" v-if="restaurant.delivery_min && restaurant.delivery_max">
                                    <a class="nav-link active" v-b-popover.hover.top="'O tempo estimado de entrega e o valor da entrega para cada zona é responsabilidade do estabelecimento!'">
                                        <i class="fa fa-location-arrow pr-1" style="color: #ffd953;"></i> {{ restaurant.distance.toFixed(0) }} KM •
                                        <i class="fa fa-motorcycle pr-1 ml-1" style="color: #ffd953;"></i> {{restaurant.delivery_min}} · {{restaurant.delivery_max}}
                                    </a>
                                </li>
                                <li class="nav-item ratings">
                                    <a class="nav-link">
                                      <span v-if="restaurant.stars != null">
                                          <star-rating v-model="restaurant.stars" :inline="true" active-color="#ffd953" :star-size="18" :read-only="true" :increment="0.01" :fixed-points="2" :border-width="2" border-color="rgba(255, 180, 45, 0.8)" :show-rating="false"></star-rating>
                                          <!--<i v-for="(n) in Math.round(restaurant.stars)" :key="n" class="fa fa-star"></i>
                                              <span v-if="Math.round(restaurant.stars) < 5">
                                                  <i v-for="(i) in (5 - Math.round(restaurant.stars))" :key="i" class="fa fa-star-o"></i>
                                              </span>-->
                                      </span>
                                      <span v-else-if="restaurant.stars == null">
                                          <p style="color: #ffb42d;"><strong>NOVO!</strong> <i class="fa fa-fire ml-1"></i></p>
                                      </span>
                                    </a>
                                 </li>
                            </ul>                        
                        <div class="payment-options">
                          <ul class="nav nav-inline">
                            <li v-for="(j) in restaurant.Payments" :key="j.id" v-if="j.is_online == 0">
                              <img :src="j.img" :width="img.width" :height="img.height"/>
                            </li>
                         </ul>
                       </div>
                    </div>                    
                    </b-col>
                </b-row>
            </b-container>
        </div>
    </section>
    <!-- end:Inner page hero -->
</template>

<script>
export default {
  props: ['restaurant'],
  data () {
    return {
      img: {
        height: '24px',
        width: '33px'
      },
      frete: false,
      delivery: false
    }
  },
  methods: {
    getFrete (restaurant) {
      if (typeof restaurant.frete === 'string') {
        this.frete = true
        return this.frete
      }
      return this.frete
    }
  },
  filters: {
    category (categories) {
      return categories.map(category => category.name).join(' • ')
    }
  },
  created () {
    this.getFrete(this.restaurant)
  }
}
</script>

<style scoped>
/* ==========================
*  Inner pages bg-image resolution: 1670x480
============================*/
.bg-image {
  background: #0B486B;  /* Old Browsers */
  background: -webkit-linear-gradient(to left, rgba(255, 180, 45, 0.8), #243B55);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, rgba(255, 180, 45, 0.8), #243B55); /* IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.inner-page-hero {
  padding-top: 5%;
  padding-bottom: 5%;
}

/********* profile banner part ***********/

.profile-banner figure img {
    width: 100%;
}

.image-wrap {
    border-radius: 15px;
    padding: 10px;
    float: center;
    background: transparent;
}

.image-wrap figure {
    background: #fff;
    border-radius: 2px;
    display: block;
    overflow: hidden;
    float: left;
    margin-bottom: 0;
}

.image-wrap figure img {
    max-width: 100%;
    display: block;
}

.profile p {
    color: #fff;
}

.banner figure {
    margin-bottom: 0;
}

.profile-desc h6 {
    font-size: 26px;
    line-height: 31px;
    color: #fff;
}

/* .profile .right-text {} */

.profile-img {
    width: 24.7%;
}

.profile .right-text span {
    margin-bottom: 8px;
    display: inline-block;
}

.profile-desc h6 {
    display: inline-block;
    margin-right: 3rem;
    margin-bottom: 10px;
}

.profile-desc a.btn {
    display: inline-block;
    padding: 3px 20px;
    background-color: #82b21c;
    vertical-align: text-bottom;
    font-size: 13px;
    color: #fff;
}

.profile-desc .right-text {
    width: 100%;
    border-right: 0;
    color: rgba(255, 255, 255, .5);
    margin-top: 30px;
}

.profile-desc .nav-link {
    padding-top: 0;
    padding-right: 1rem;
    padding-bottom: 0.5rem;
    padding-left: 0;
}

.profile-desc .nav-item a {
    color: #fff;
}

.profile-desc .nav-item.ratings a span i {
    color: #ffd953;
    font-size: 20px;
}

@media (min-width:320px) and (max-width:768px) {
    .profile-desc .right-text {
        margin-top: 5%;
        text-align: center;
    }
    .profile-desc h6 {
        margin-right: 0;
    }
    .profile-desc a.btn {
        float: right;
    }
    .image-wrap img {
        width: auto;
    }
    .profile-img {
        width: 100%;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .profile-desc .right-text {
        margin-top: 0;
    }
}

/* .payment-options */

.payment-options ul li {
  display: inline-block;
  margin-right: 8px;
}
.payment-options ul {
  margin-top: 10px;
  float: left;
  width: 100%;
}

.badge-cash-back {
    font-weight: 400;
    font-size: 14px;
    color: #fff;
    background-color: #387ef5;
    border-radius: 2px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 3px;
    padding-bottom: 3px;
    margin-right: 20px;
}

.badge-free-shipping {
    font-weight: 400;
    font-size: 16px;
    color: #fff;
    background-color: green;
    border-radius: 2px;
    padding: 3px;
}

.badge-not-delivery {
    font-weight: 400;
    font-size: 16px;
    color: #fff;
    background-color: red;
    border-radius: 2px;
    padding: 3px;
}

.badge-only-delivery {
    font-weight: 400;
    font-size: 16px;
    color: #fff;
    background-color: green;
    border-radius: 2px;
    padding: 3px;
}


/* RIBBON BADGES */

    /* OPEN RESTAURANT */

.open-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

    /* ./OPEN RESTAURANT */

    /* CLOSED RESTAURANT */

.closed-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.closed-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: linear-gradient(#F70505 0%, #B22222 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -31px;
}
.closed-ribbon span::before {
  content: "";
  position: absolute; left: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid #B22222;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}
.closed-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #B22222;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}

 /* ./RIBBON BADGES */
</style>



