import Vue from 'vue'
import * as types from '../mutation-types'

// You can use it as state property
const state = {
  isLoggedIn: localStorage.getItem('token') && localStorage.getItem('user'),
  user: {}
}

// You can use it as a state getter function (probably the best solution)
const getters = {
  isLoggedIn: state => {
    return state.isLoggedIn
  },
  user: state => {
    return localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {}
  },
  newUser: state => {
    return state.user.isNew
  }
}

// Mutation for when you use it as state property
const mutations = {
  [types.LOGIN] (state) {
    state.loginPending = true
  },
  [types.LOGIN_SUCCESS] (state, payload) {
    // console.log('LOGIN_SUCCESS - ', 'payload: ', payload)
    localStorage.setItem('token', payload.token)
    localStorage.setItem('user_email', payload.user.email)
    localStorage.setItem('user', JSON.stringify(payload.user))
    state.isLoggedIn = true
    state.user = payload.user
    state.loginPending = false
  },
  [types.LOGOUT] (state) {
    localStorage.setItem('token', process.env.TMP_TOKEN)
    localStorage.removeItem('user_email')
    localStorage.removeItem('user')
    // localStorage.removeItem('lastUsed')
    state.isLoggedIn = false
    state.user = false
  }
}

const actions = {
  login: ({
    commit
  }, payload) => {
    commit(types.LOGIN) // show spinner
    return new Promise(resolve => {
      // console.log('authenticate - ', 'payload: ', payload)
      Vue.axios.post('/authenticate', payload).then((response) => {
        return response.data
      }).then((response) => {
        if (response.status) {
          commit(types.LOGIN_SUCCESS, {
            user: response.data,
            token: response.token
          })
        }
        resolve(response)
      })
    })
  },

  socialLogin: ({
    commit
  }, payload) => {
    commit(types.LOGIN) // show spinner
    return new Promise(resolve => {
      console.log('loginfacebook - ', 'payload: ', payload)
      Vue.axios.post('/loginfacebook', payload).then((response) => {
        return response.data
      }).then((response) => {
        if (response.status) {
          commit(types.LOGIN_SUCCESS, {
            user: response.data,
            token: response.token
          })
        }
        resolve(response.data)
      })
    })
  },

  logout: ({
    commit
  }) => {
    commit(types.LOGOUT)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



