<template>
    <div class="widget clearfix">
        <div class="widget-heading">
            <h3 class="widget-title text-dark">
                Categorias populares
                <i class="fa fa-fire pull-right"></i>
            </h3>
            <div class="clearfix"></div>
        </div>
        <div class="widget-body">
            <ul class="tags">
                <li>
                    <a href="#" class="tag">
                        Pizzaria
                    </a>
                </li>
                <li>
                    <a href="#" class="tag">
                        Hamburgueria
                    </a>
                </li>
                <li>
                    <a href="#" class="tag">
                        Japonesa
                    </a>
                </li>
                <li>
                    <a href="#" class="tag">
                        Massas
                    </a>
                </li>
                <li>
                    <a href="#" class="tag">
                        Açaí
                    </a>
                </li>
                <li>
                    <a href="#" class="tag">
                        Pastelarias
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {}
</script>

<style scoped>
.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.widget-heading .widget-title {
    font-size: 16px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.widget-body {
    padding: 20px;
}

/*==============================
* TAGS
==============================*/

.tags {
    overflow: hidden;
    margin: 0;
    padding: 0;
    list-style: none;
}

.tags li {
    float: left;
}

.tag {
    display: inline-block;
    font-size: 14px;
    line-height: 26px;
    position: relative;
    height: 28px;
    margin: 0 10px 10px 0;
    padding: 0 20px 0 15px;
    -webkit-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
    border: 1px solid #eaebeb;
    border-radius: 2px;
    background: #ffffff;
    color: inherit;
    font-weight: normal;
}

.tag:after,
.tag:before {
    -webkit-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
    background: #fff;
}

.tag:before {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 6px;
    height: 6px;
    border-radius: 10px;
    box-shadow: inset 0 1px rgba(0, 0, 0, .25);
}

.tag:after {
    border-top: 13px solid transparent;
    border-bottom: 13px solid transparent;
}

.tag:hover {
    color: #fff;
    border-color: #ffb42d;
    background-color: #ffb42d;
}
</style>



