<template>
  <div class="profile page-wrapper">
    <app-top-links></app-top-links>
    <app-inner-page-hero :restaurant="restaurant" :ratings="ratings"></app-inner-page-hero>
    <b-card bg-variant="transparent" no-body>
      <b-tabs pills v-model="tabIndex">
        <b-tab title="Cardápio" :title-link-class="linkClass(0)">
          <b-container fluid class="m-t-30">
            <b-row>
              <div class="col-xs-12 col-sm-8 col-md-8 col-lg-9">
                <app-widget-list-items :cuisines="cuisines" :items="items"></app-widget-list-items>
                <!--<app-widget-popular-tags></app-widget-popular-tags>-->
              </div>
              
                <div class="col-xs-12 col-sm-4 col-md-4 col-lg-3">
                <app-widget-cart></app-widget-cart>
                
              </div>
              <!--<div class="col-xs-12 col-md-12 col-lg-3">
                <app-sidebar-choose-cusine :cuisines="cuisines"></app-sidebar-choose-cusine>
              </div>-->
            </b-row>
            <!-- end:row -->
          </b-container>
        </b-tab>
        <b-tab title="Horário de Funcionamento" :title-link-class="linkClass(1)">
          <ul style="color: black;">
            <li>Segunda: {{restaurant.monday}}</li>
            <li>Terça: {{restaurant.tuesday}}</li>
            <li>Quarta: {{restaurant.wednesday}}</li>
            <li>Quinta: {{restaurant.thursday}}</li>
            <li>Sexta: {{restaurant.friday}}</li>
            <li>Sábado: {{restaurant.saturday}}</li>
            <li>Domingo: {{restaurant.sunday}}</li>
          </ul>
        </b-tab>
        <b-tab title="Avaliações" :title-link-class="linkClass(2)">
          <b-container>
            <b-row class="mt-4 mb-5">
              <b-col cols="12" sm="6" class="mb-4" v-for="(rate) in ratings" :key="rate.id">
                <div class="ratings-wrap">
                  <b-row>
                    <b-col cols="12" md="3" class="profile-img">
                              <figure v-if="rate.user.img">
                                  <b-img rounded="circle" :src="rate.user.img" :width="100" :height="100"/>
                              </figure>
                              <figure v-if="!rate.user.img">
                                <b-img rounded="circle" src=".././assets/img/profile/user_male.png" :width="100" :height="100"/>
                              </figure>
                      </b-col>
                      <ul style="color: black;">
                        <li style="font-weight: 500;">{{rate.user.first_name}} {{rate.user.last_name}}</li>
                        <li style="font-size: 12px;" class="pt-1 pb-1">{{rate.createdAt}}</li>
                        <li><i class="fa fa-cutlery pr-1"></i>Comida: <i style="color: #ffd953;" class="fa fa-star"></i> {{rate.rate_food}}</li>
                        <li><i class="fa fa-thumbs-up pr-1"></i>Custo/Benefício: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate }}</li>
                        <li><i class="fa fa-cube"></i> Embalagem: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate_packing }}</li>
                        <li><i class="fa fa-clock-o pr-1"></i>Tempo de Entrega: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate_delivery }}</li>
                        <li><i class="fa fa-line-chart pr-1"></i>Média: <i style="color: #ffd953;" class="fa fa-star"></i> média aqui</li>
                        <li v-if="rate.description"><i class="fa fa-comment pr-1"></i>Comentário: "<span style="font-size: 13px; font-weight: 700;">{{ rate.description }}"</span></li>
                      </ul>
                  </b-row>
                </div>
              </b-col>
              <b-col cols="12" sm="6" class="mb-4" v-if="!ratings || !ratings.length">
                <h3 style="color: black;">Ainda não possuímos nenhuma avaliação. <i class="fa fa-frown-o pl-1"></i></h3>
              </b-col>
            </b-row>
          </b-container>
        </b-tab>
    <!--<app-breadcrumb></app-breadcrumb>-->

  </b-tabs>
  </b-card>
    <!-- end:Container -->
    <!--<app-modal-cart-addon :items="items"></app-modal-cart-addon>-->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppTopLinks from './partials/TopLinks'
import AppInnerPageHero from './partials/profile/InnerPageHero'
import AppBreadcrumb from './partials/profile/Breadcrumb'
import AppSidebarChooseCusine from './partials/profile/SidebarChooseCusine'
import AppWidgetPopularTags from './partials/WidgetPopularTags'
import AppWidgetListItems from './partials/profile/WidgetListItems'
import AppWidgetCart from './partials/profile/WidgetCart'
import AppModalCartAddon from './partials/profile/ModalCartAddon'

export default {
  props: ['id'],
  data () {
    return {
      restaurant: [],
      cuisines: [],
      items: [],
      ratings: [],
      tabIndex: 0
    }
  },
  components: {
    AppTopLinks,
    AppInnerPageHero,
    AppBreadcrumb,
    AppSidebarChooseCusine,
    AppWidgetPopularTags,
    AppWidgetListItems,
    AppWidgetCart,
    AppModalCartAddon
  },
  computed: {
    ...mapGetters({
      currentPosition: 'currentPosition',
      getCartItems: 'getCartItems'
    })
  },
  methods: {
    linkClass (idx) {
      if (this.tabIndex === idx) {
        return ['bg-warning', 'text-dark']
      } else {
        return ['bg-light', 'text-dark']
      }
    },
    async getRestaurantById () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/' + this.id + '/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getCuisinesById () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'cuisines/cuisines/items/' + this.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getItemsByRestaurantId () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'items/restaurant/' + this.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getRatings () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'rating/restaurant/' + this.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async calcFrete () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.post(
          'calcula-frete', {
            destinationsLatitude: this.currentPosition.lat,
            destinationsLongitude: this.currentPosition.lng,
            restaurants_id: this.id
          })
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.getCartItems.length > 0 && to.fullPath === '/restaurantes') {
      this.$swal({title: 'Sair', text: 'Deseja realmente sair desta tela? Ao sair seu pedido será excluído do carrinho', type: 'warning', showCancelButton: true, confirmButtonColor: '#3085d6', cancelButtonColor: '#d33', confirmButtonText: 'Sim, sair', cancelButtonText: 'Cancelar'})
      .then((result) => {
        if (result.value) {
          this.$swal('Você saiu', 'Seu carrinho foi esvaziado!', 'success')
          this.$store.dispatch('emptyCart')
          next()
        }
      })
    } else {
      next()
    }
  },
  created () {
    /* calcFrete */
    this.calcFrete().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.$store.commit('setFrete', response1)
    })
    /* ./calcFrete */
    /* getRestaurantById */
    this.getRestaurantById().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.restaurant = response1
      this.$store.commit('setRestaurant', response1)
    })
    /* ./getRestaurantById */
    /* getCuisinesById */
    this.getCuisinesById().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.cuisines = response1
      this.$store.commit('setCuisines', response1)
    })
    /* ./getCuisinesById */
    /* getItemsByRestaurantId */
    this.getItemsByRestaurantId().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.items = response1
      this.$store.commit('setItems', response1)
    })
    /* ./getItemsByRestaurantId */
    /* getRatings */
    this.getRatings().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.ratings = response1
    })
    /* ./getRatings */
  }
}
</script>

<style scoped>

</style>



