import Vue from 'vue'
import GoogleMapsLoader from 'google-maps'
import * as types from '../mutation-types'

// You can use it as state property
const state = {
  currentPosition: {
    lat: -18.918889,
    lng: -48.276944
  },
  lastUsed: localStorage.getItem('lastUsed') ? JSON.parse(localStorage.getItem('lastUsed')) : {},
  allCities: [],
  allStates: []
}

// You can use it as a state getter function (probably the best solution)
const getters = {
  allCitiesByUF: state => (uf) => {
    let _cities = state.allCities.filter(city => city.value.uf === uf)
    return _cities
  },
  allStates: state => {
    return state.allStates
  },
  currentPosition: state => {
    return state.currentPosition
  },
  lastUsed: state => {
    return state.lastUsed
  }
}

// Mutation for when you use it as state property
const mutations = {
  [types.SET_CITIES] (state, payload) {
    state.allCities = payload.cities
  },
  [types.SET_CURRENT_POSITION] (state, payload) {
    state.currentPosition = payload
  },
  [types.SET_LAST_USED] (state, payload) {
    localStorage.setItem('lastUsed', JSON.stringify(state.lastUsed))
    state.lastUsed = payload.lastUsed
  },
  [types.SET_STATES] (state, payload) {
    state.allStates = payload.states
  }
}

const actions = {
  setLastUsed: ({
    commit
  }, payload) => {
    commit(types.SET_LAST_USED, {
      lastUsed: payload
    })
    commit(types.SET_CURRENT_POSITION, {
      lat: payload.latitude,
      lng: payload.longitude
    })
  },
  async setCurrentPosition ({
    commit
  }) {
    if (window.navigator && window.navigator.geolocation) {
      var geolocation = window.navigator.geolocation
      geolocation.getCurrentPosition(sucesso, erro, {enableHighAccuracy: true})
    } else {
      alert('Geolocalização não suportada no seu navegador, por favor atualize!')
    }
    function sucesso (posicao) {
      commit(types.SET_CURRENT_POSITION, {
        lat: posicao.coords.latitude,
        lng: posicao.coords.longitude
      })
    }
    function erro (error) {
      console.log(error)
      commit(types.SET_CURRENT_POSITION, {
        lat: -18.914608,
        lng: -48.275380
      })
    }
    GoogleMapsLoader.KEY = process.env.GOOGLE_MAPS_KEY
    GoogleMapsLoader.load(async function (google) {
      let geocoder = await new google.maps.Geocoder()
      geocoder.geocode({
        'location': state.currentPosition
      }, function (results, status) {
        if (status === 'OK') {
          if (results[0]) {
            let formattedAddress = results[0].formatted_address.split(',')
            commit(types.SET_LAST_USED, {
              lastUsed: {
                street: (formattedAddress[0]).trim(),
                number: (formattedAddress[1].split(' - ')[0]).trim(),
                neighborhood: (formattedAddress[1].split(' - ')[1]).trim(),
                city: (formattedAddress[2].split(' - ')[0]).trim(),
                uf: (formattedAddress[2].split(' - ')[1]).trim(),
                cep: (formattedAddress[3]).trim(),
                latitude: results[0].geometry.location.lat,
                longitude: results[0].geometry.location.lng
              }
            })
          }
        }
      })
    })
  },
  setAllCities: ({
    commit
  }) => {
    Vue.axios.get('/localidades/municipios').then((response) => {
      return response.data
    }).then(response => {
      if (response) {
        return response.data.map(city => {
          return {
            text: city.nome,
            value: {
              name: city.nome,
              uf: city.microrregiao.mesorregiao.UF.sigla
            }
          }
        })
      }
      return []
    }).then(response => {
      commit(types.SET_CITIES, {
        cities: response
      })
    })
  },
  setAllStates: ({
    commit
  }) => {
    Vue.axios.get('/localidades/estados').then((response) => {
      return response.data
    }).then(response => {
      if (response) {
        return response.data.map(state => state.sigla)
      }
      return []
    }).then(response => {
      commit(types.SET_STATES, {
        states: response
      })
    })
  }

}

export default {
  state,
  getters,
  mutations,
  actions
}



