<template>
  <!-- banner part starts -->
    <section class="hero bg-image">
        <div class="hero-inner">
            <div class="container text-center hero-text font-white">
                <h1>Delivery para qualquer fome</h1>
                <!--<h5 class="font-white space-xs">Encontre restaurantes, promoções e cupons gratuitamente.</h5>-->
                <div class="banner-form">
                  <app-search-address></app-search-address>
                </div>
                <div class="steps">
                    <div class="step-item step1">
                        <b-img rounded src="../../../assets/img/home/banner/step-1.png" alt="SaciaFome" />
                        <h4>
                            <span>1. </span>Escolha a localização</h4>
                    </div>
                    <!-- end:Step -->
                    <div class="step-item step2">
                        <b-img rounded src="../../../assets/img/home/banner/step-2.png" alt="SaciaFome" width="34px" />
                        <h4>
                            <span>2. </span>Encomendar comida</h4>
                    </div>
                    <!-- end:Step -->
                    <div class="step-item step3">
                        <b-img rounded src="../../../assets/img/home/banner/step-3.png" alt="SaciaFome" width="34px" />
                        <h4>
                            <span>3. </span>Entrega ou retirada</h4>
                    </div>
                    <!-- end:Step -->
                </div>
                <!-- end:Steps -->
            </div>
        </div>
        <!--end:Hero inner -->
    </section>
    <!-- banner part ends -->
</template>

<script>
import AppSearchAddress from './SearchAddress'

export default {
  components: {
    AppSearchAddress
  },
  methods: {
    search () {
      this.$router.push('/restaurants')
    }
  }
}
</script>

<style scoped>
/* ==========================
*   Hero
============================*/
.bg-image {
  background: url("../../../assets/img/home/banner/cuisine-banner.jpg") no-repeat center center;
  background-size: cover;
}

.hero {
  padding-top: 10%;
  padding-bottom: 5%;
  text-align: center;
  position: relative;
}

.hero h1 {
  font-family: "Give You Glory", cursive;
  color: #fff;
  font-size: 3.7em;
  font-weight: 700;
}

.hero:before {
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: rgba(7, 6, 29, 0.37);
}

.hero .hero-inner {
  position: relative;
  z-index: 1;
}

/* ==========================
*   Hero steps
============================*/

.step-item {
  display: inline-block;
  margin: 0 40px 0;
  position: relative;
}

.banner-form {
  margin: 0 auto 30px;
}

.step-item img {
  display: block;
  margin: 0 auto 15px;
}

.step-item h4 {
  font-family: "Give You Glory", cursive;
  color: #fff;
  font-size: 21px;
}

.step-item h4 span {
  color: #ffb42d;
}

.step1:after,
.step2:after {
  position: absolute;
  content: "";
  right: -58%;
  top: -20%;
  width: 130px;
  height: 32px;
  background: url("../../../assets/img/home/banner/arrow.png");
}

.step2:after {
  background: url("../../../assets/img/home/banner/arrow-dotted.png");
  height: 25px;
  right: -55%;
  /* top: -5%; */
}

@media (max-width: 568px) and (min-width: 280px) {
  .step1:after,
  .step2:after {
    display: none;
  }
  .hero h1 {
    font-size: 2.2em;
    padding-top: 75px;
  }
  .bg-image {
    background: url("../../../assets/img/home/banner/banner-min.jpg") no-repeat center center;
    background-size: cover;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .step-item {
    margin: 0 30px 0;
  }
}
</style>



