<template>
    <div class="orders-page wrapper mt-5 pt-4">
        <b-container class="mt-4 pb-5">
            <span class="title"><i class="fa fa-th-list pr-2"></i> Meus Pedidos ({{orders.length}})</span>
            <b-row class="mt-3 mb-5">
                <b-col cols="12" sm="12" class="mb-4">
                    <b-card border-variant="dark" bg-variant="light" class="mt-1 mb-3" v-for="(order,index) in orders" :key="index.id">
                      <b-img center rounded="circle" width="200px" height="200px" :src="order.orders.restaurant.img" :alt="order.orders.restaurant.name"/>
                      <i v-if="order.status === '1'" style="color: blue;" class="fa fa-4x fa-exclamation-circle pull-left mt-4"></i>
                      <i v-if="order.status === '2'" style="color: orange;" class="fa fa-4x fa-clock-o pull-left mt-4"></i>
                      <i v-if="order.status === '3'" style="color: black;" class="fa fa-4x fa-motorcycle pull-left mt-4"></i>
                      <i v-if="order.status === '4'" style="color: green;" class="fa fa-4x fa-check-circle pull-left mt-4"></i>
                      <i v-if="order.status === '5'" style="color: red;" class="fa fa-4x fa-times-circle pull-left mt-4"></i>
                      <i v-if="order.status === '6'" style="color: black;" class="fa fa-4x fa-cube pull-left mt-4"></i>
                      <div class="order-info">
                        <h3>{{order.orders.restaurant.name | Upper}}</h3>
                        <h4>{{ getOrderStatus( order.status ) }}</h4>
                        <h5 v-if="isDelivery(order)">ENTREGA: {{ order.orders.restaurant.delivery_min }} • {{ order.orders.restaurant.delivery_max }}</h5>
                        <h5 v-else>TEMPO DE PREPARO: {{order.orders.restaurant.delivery_min}} • {{order.orders.restaurant.delivery_max}}</h5>
                        <br v-if="order.orders.rating"/>
                        <span v-if="order.orders.rating">
                        AVALIAÇÃO:
                        <i style="color: #ffb42d;" class="fa fa-star"></i>
                        {{ getMedia(order.orders.rating) }}
                      </span>
                      </div>
                      <hr>
                      <div class="order-info">
                        <h4>TOTAL: {{getTotal(order.total)}}</h4>
                        <h5 v-if="order.status === '1'">TEMPO DE ESPERA: {{order.waitingTime}}</h5>
                        <span class="pull-left mt-1" v-if='order.origin == "web"'><i class="fa fa-desktop pr-1" style="color: black;"></i> PEDIDO REALIZADO UTILIZANDO COMPUTADOR</span>
                        <span v-else-if='order.origin == "cordova,mobile,android"' class="pull-left mt-1"><i class="fa fa-android pr-1" style="color: black;"></i> PEDIDO REALIZADO UTILIZANDO CELULAR</span>
                      </div>
                        <b-btn disabled class="pull-right" size="lg" variant="success"><i class="fa fa-info pr-1"></i> DETALHES</b-btn>
                    </b-card>
                    <b-card v-if="(!orders || !orders.length)">
                      Você ainda não realizou nenhum pedido.
                    </b-card>
                </b-col>
            </b-row>


        </b-container>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  components: {},
  data () {
    return {
      orders: [],
      orderTotal: 0,
      statusName: ['', 'PENDENTE - AGUARDANDO CONFIRMAÇÃO',
        'EM PREPARO - JÁ FOI CONFIRMADO',
        'SAIU PARA ENTREGA',
        'CONCLUÍDO - PEDIDO ENTREGUE',
        'CANCELADO - PEDIDO CANCELADO',
        'PRONTO - CLIENTE VEM BUSCAR'
      ]
    }
  },
  computed: {
    ...mapGetters({
      user: 'user'
    })
  },
  methods: {
    async getOrders () {
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
      let response = await _axios.get(
        'orders/user/' + this.user.id
      )
      return response
    },
    getOrderStatus (status) {
      return this.statusName[status]
    },
    isDelivery (order) {
      if (parseInt(order.flag_delivery) > 0) {
        return true
      }
      if (parseInt(order.flag_delivery) > 0 && parseInt(order.status) < 4) {
        return true
      }
      return false
    },
    getTotal (total) {
      if (typeof total === 'string') {
        // this.orderTotal = parseFloat(total).toFixed(2)
        this.orderTotal = parseFloat(total).toFixed(2).split('.')
        this.orderTotal[0] = 'R$ ' + this.orderTotal[0].split(/(?=(?:...)*$)/).join('.')
        return this.orderTotal.join(',')
      }
      return this.orderTotal
    },
    getMedia (rate) {
      if (rate.rate_delivery) {
        let media = (rate.rate_custo + rate.rate_food + rate.rate_delivery) / 3
        return media.toFixed(2)
      } else {
        let media = (rate.rate_custo + rate.rate_food) / 2
        return media.toFixed(2)
      }
    }
  },
  filters: {
    Upper (value) {
      return value.toUpperCase()
    }
  },
  created () {
    /* getOrders */
    this.getOrders().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.orders = response1
    })
    /* ./getOrders */
  }
}
</script>

<style scoped>
.title {
  color: #4f4f4f;
  font-weight: 600;
  font-size: 25px;
  padding-bottom: 20px;
}

.order-info {
  margin-top: 0.4rem;
  margin-left: 5rem;
}

</style>



