<template>
    <section class="contact-page inner-page">
        <div class="container">
            <div class="row row-centered">
                <!-- REGISTER -->
                <div class="col-md-3"></div>
                <div class="col-md-9">
                    <div class="widget-body">
                        <form>
                            <div class="row">
                                <div class="form-group col-sm-6" style="text-align: center;">
                                    <label for="email" class="label-password"><i class="fa fa-lock"></i> Esqueci minha senha</label><br/>
                                    <span style="font-size: 15px;">Informe seu email e receberá a nova senha dentro de instantes</span>
                                    <input class="form-control input-password" type="text" id="email" v-model="user.email">
                                    <b-button class="button-password" @click="send()" >ENVIAR</b-button>
                                </div>
                            </div>
                        </form>
                    </div>
                    <!-- /REGISTER -->
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
  data () {
    return {
      user: {}
    }
  },
  methods: {
    send () {
      this.$http.get(`/users/forgot-password/${this.user.email}`).then((response) => {
        response = response.data
        if (response.status) {
          this.$toasted.success(response.message).goAway(3000)
        } else {
          this.$toasted.error(response.message).goAway(3000)
        }
      })
    }
  }
}
</script>

<style scoped>

.label-password {
    font-size: 32px;

}

.input-password {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-password {
    border: 0 !important;
    color: white;
    cursor: pointer;
    height: 50px;
    line-height: 20px;
    border-radius: 8px;
    box-sizing: border-box;
    display: block;
    font-size: 1.6em;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 12px;
    padding-left: 21px;
    padding-top: 12px;
    width: 100%;
    background-color: #ffb42d;
}

</style>



