<template>
    <div id="restaurant-list">
        <div class="bg-gray restaurant-entry">
          <b-badge variant="transparent" class="open-ribbon" v-if="restaurant.open == 1"><span>ABERTO</span></b-badge>
          <b-badge variant="transparent" class="closed-ribbon" v-else-if="restaurant.open == 0"><span>FECHADO</span></b-badge>
            <b-row>
                <div class="col-sm-12 col-md-12 col-lg-8 text-sm-left">
                    <div class="entry-logo">
                        <img class="img-fluid" :src="restaurant.img" alt="Restaurant logo"/>
                    </div>
                    <!-- end:Logo -->
                    <div class="entry-dscr">
                        <router-link :to="'/restaurantes/' + restaurant.id">
                        <h5>
                            {{restaurant.name}}
                        </h5>
                        </router-link>
                        <span>{{restaurant.Categories | category}}</span>
                        <ul class="list-inline">
                            <li class="list-inline-item">
                                <i class="fa fa-motorcycle"></i><span class="pl-1">{{restaurant.delivery_min}} · {{restaurant.delivery_max}}</span>
                            </li>
                        </ul>
                    </div>
                    <!-- end:Entry description -->
                </div>
                <div class="col-sm-12 col-md-12 col-lg-4 text-sm-center">
                    <div class="right-content bg-white">
                        <div class="right-review">
                            <div class="rating-block">
                                <span v-if="restaurant.stars != null">
                                    <star-rating v-model="restaurant.stars" :inline="true" :star-size="15" :read-only="true" :increment="0.01" :fixed-points="2" :show-rating="false"></star-rating>
                                    <!--<i v-for="(n) in Math.round(restaurant.stars)" :key="n" class="fa fa-star"></i>
                                        <span v-if="Math.round(restaurant.stars) < 5">
                                            <i v-for="(i) in (5 - Math.round(restaurant.stars))" :key="i" class="fa fa-star-o"></i>
                                        </span>-->
                                </span>
                                <span v-else-if="restaurant.stars == null">
                                    <p style="color: #ffb42d;"><strong>NOVO!</strong> <i class="fa fa-fire ml-1"></i></p>
                                </span>
                            </div>
                            <p> {{restaurant.amount_reviews}}</p>
                            <router-link :to="'/restaurantes/' + restaurant.id">
                            <b-btn class="btn btn-theme-dash">
                            Ver Cardápio</b-btn></router-link>
                        </div>
                    </div>
                    <!-- end:right info -->
                </div>
            </b-row>
            <!--end:row -->
        </div>
        <!-- end:Restaurant entry -->

    </div>
</template>

<script>
export default {
  props: ['restaurant'],
  filters: {
    category (categories) {
      return categories.map(category => category.name).join(' · ')
    }
  }
}
</script>

<style scoped>

/* RIBBON BADGES */

    /* OPEN RESTAURANT */

.open-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

    /* ./OPEN RESTAURANT */

    /* CLOSED RESTAURANT */

.closed-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.closed-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: linear-gradient(#F70505 0%, #B22222 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -31px;
}
.closed-ribbon span::before {
  content: "";
  position: absolute; left: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid #B22222;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}
.closed-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #B22222;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}

 /* ./RIBBON BADGES */

/*==============================
*
* Restaurants
*
==============================*/

.restaurant-entry {
    border: 1px solid #eaebeb;
    border-radius: 2px;
    margin-bottom: 20px;
}

.restaurant-entry .entry-logo {
    float: left;
    width: 110px;
    height: auto;
    border: 1px solid #eaebeb;
    border-radius: 2px;
    margin-top: 15px;
    margin-left: 15px;
    overflow: hidden;
}

.restaurant-entry .entry-logo img {
    max-width: 100%;
    display: block;
}

.restaurant-entry .entry-dscr {
    padding-left: 145px;
    margin-top: 15px;
}

.restaurant-entry .entry-dscr h5 a {
    color: #414551;
    font-weight: 500;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.restaurant-entry .entry-dscr ul li {
    font-size: 12px;
    margin-top: 7px;
}

.restaurant-entry .right-review {
    padding: 15px 10px;
    border-left: 1px solid #eaebeb;
    min-height: 145px;
    background-color: #fafaf8;
}

.restaurant-entry .right-review p {
    color: #414551;
    font-weight: 500;
    margin: 0 0 10px;
}

.restaurant-entry .right-review .rating-block {
    color: #ffb42d;
    background: transparent;
    border: none;
    padding: 5px 15px 5px;
}

@media (min-width: 320px) and (max-width: 991px) {
    .restaurant-entry .entry-logo {
        float: none;
        text-align: center;
        margin: 10px auto;
    }
    .restaurant-entry .entry-dscr {
        padding: 0 15px;
        text-align: center;
    }
    .restaurant-entry .right-review {
        padding: 15px 10px;
        border-left: transparent;
        border-top: 1px solid #eaebeb;
    }
}
</style>



