<template>
  <div id="app">
    <template v-if="underMaintenance">
      <h4 class="text-center">Servidor em manutenção, retornaremos em breve...</h4>
    </template>
    <template v-else>
      <app-header></app-header>
      <div class="main">
        <router-view></router-view>
      </div>
      <app-footer></app-footer>
      <back-to-top></back-to-top>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import AppHeader from '@/components/root/header/Header'
import AppHowGetApps from '@/components/root/HowGetApps'
import AppFooter from '@/components/root/Footer'

export default {
  name: 'app',
  components: {
    AppHeader,
    AppHowGetApps,
    AppFooter
  },
  data () {
    return {
      underMaintenance: false
    }
  },
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn'
    })
  },
  methods: {
    ...mapActions({
      allCities: 'setAllCities',
      allStates: 'setAllStates',
      currentPosition: 'setCurrentPosition'
    })
  },
  created () {
    if (!this.isLoggedIn) {
      this.allCities()
      this.allStates()
      this.currentPosition()
    }
  }
}
</script>

<style scoped>
#app {
  background: rgba(252, 251, 249, 0.68);
  font-size: 16px;
  color: #4d4f56;
  font-weight: 400;
  overflow-x: hidden;
}
</style>



